import $ from 'jquery';
import AppModalDialogView from '../views/appModalDialogView/AppModalDialogView';
import ISubject from '../services/ISubject';
import SHORTCUT_TOOL_BUTTONS, { SHORTCUT_MISC_BUTTONS } from '../models/AppShortcutButtons';
import ToolShortcutButton from '../views/appTools/toolShortcutButton/ToolShortcutButton';
import AppToolTipService from '../services/AppToolTipService';
import Event, { EVENT_ENUM } from '../models/AppEvent';
import { KEYBOARD_SHORTCUTS_PROPERTIES_ENUM } from '../models/AppKeyboardShortcutModel';

const ALLOWED_KEYS = /\w/;

export default class AppModalDialogController extends ISubject {
  constructor(keyboardService) {
    super();
    this.element = new AppModalDialogView();
    this.buttons = [];
    this.currentButton = null;
    this.keyboardService = keyboardService;
    this.container = null;
    this.windowLocked = false;
    this.initialShortcuts = null;
  }

  renderShortcutButtons() {
    Object.keys(SHORTCUT_TOOL_BUTTONS).forEach((val) => {
      const {
        key, id, label, text,
      } = SHORTCUT_TOOL_BUTTONS[val];
      const button = new ToolShortcutButton(key, id, label, text);
      this.buttons.push(button);
      button.render('#modal-dialog__fieldset--tools');
    });
    Object.keys(SHORTCUT_MISC_BUTTONS).forEach((val) => {
      const {
        key, id, label, text,
      } = SHORTCUT_MISC_BUTTONS[val];
      const button = new ToolShortcutButton(key, id, label, text);
      this.buttons.push(button);
      button.render('#modal-dialog__fieldset--misc');
    });
  }

  refreshShortcutButtons() {
    for (let i = 0; i < this.buttons.length; i += 1) {
      const button = this.buttons[i];
      button.currentLabel = button.label;
      $(`#${button.buttonId}`).html(button.label);
    }
  }

  addButtonToModalWindow() {
    $(`#${this.element.modalDialogId}`).dialog(
      'option', 'buttons',
      [
        {
          text: 'Ok',
          click: () => {
            $(`#${this.element.modalDialogId}`).dialog('close');
          },
        },
      ],
    );
  }

  getFinalShortcuts() {
    const buttonFinalLabels = [];
    this.buttons.forEach(button => buttonFinalLabels.push({
      buttonId: button.buttonId,
      buttonLabel: button.currentLabel,
    }));
    return buttonFinalLabels;
  }

  setInitialState(initialState) {
    const { currentbuttonShortcuts } = initialState;
    this.initialShortcuts = currentbuttonShortcuts;
  }

  init(container) {
    this.container = container;
    this.element.render(this.container);
    this.renderShortcutButtons();
    AppToolTipService.addToolTipByClass(
      'shortcut-button',
      'button',
      'Click to change the default shortcut. '
      + 'Enter key or key combination to set as custom shortcut. '
      + 'Please, use only English characters, digits, \'SHIFT\', \'CTRL\' or \'ALT\' buttons',
    );
    this.addButtonToModalWindow();
    if (this.initialShortcuts) {
      this.initialShortcuts.forEach((shortcut) => {
        for (let i = 0; i < this.buttons.length; i += 1) {
          const button = this.buttons[i];
          if (button.buttonId === shortcut.buttonId) {
            button.currentLabel = shortcut.buttonLabel;
            $(`#${button.buttonId}`).html(button.currentLabel);
          }
        }
      });
    }
  }

  disableCurrentButton() {
    this.removeWaiting();
    this.currentButton = null;
    $('body').off({
      keydown: this.handleShortcutInput.bind(this),
    });
    const event = new Event(EVENT_ENUM.MODAL_WINDOW_UNLOCKED);
    this.notify(event);
    this.windowLocked = false;
  }

  removeWaiting() {
    this.buttons.forEach((button) => {
      $(`#${button.buttonId}`).removeClass('waiting');
    });
  }

  static createButtonLabel(key, ctrlKey, shiftKey, altKey) {
    const ctrl = ctrlKey ? 'CTRL + ' : '';
    const shift = shiftKey ? 'SHIFT + ' : '';
    const alt = altKey ? 'ALT + ' : '';
    return `${ctrl}${shift}${alt}${key.toUpperCase()}`;
  }

  handleShortcutInput(e) {
    e.preventDefault();
    if (this.currentButton) {
      e.stopPropagation();
      const {
        key, ctrlKey, shiftKey, altKey,
      } = e;
      if (key.match(ALLOWED_KEYS) && key.length === 1) {
        const eventValue = JSON.stringify({
          key: key.toUpperCase(), altKey, shiftKey, ctrlKey,
        });
        const result = this.keyboardService.setNewShortcutValue(
          this.currentButton.buttonId, eventValue,
        );
        const { changed, property } = result;
        if (changed && property === this.currentButton.buttonId) {
          $('#modal-dialog__error-message').html('You have successfully changed the shortcut!');
          const label = AppModalDialogController.createButtonLabel(key, ctrlKey, shiftKey, altKey);
          $(`#${this.currentButton.buttonId}`).html(label);
          this.currentButton.currentLabel = label;
        } else if (!changed && property === this.currentButton.buttonId) {
          $('#modal-dialog__error-message').html('This shortcut is already applied to this property!');
        } else if (!changed && property !== this.currentButton.buttonId) {
          $('#modal-dialog__error-message').html('This shortcut is already applied to another property! Please, use a different one.');
        }
        this.disableCurrentButton();
      } else {
        $('#modal-dialog__error-message').html('Invalid key code. Please, use only Latin letters or digits.');
      }
    }
    return false;
  }

  handleShortcutButtonClick(e) {
    const lockEvent = new Event(EVENT_ENUM.MODAL_WINDOW_LOCKED);
    this.notify(lockEvent);
    this.windowLocked = true;
    const eventTarget = e.target;
    let buttonKey;
    Object.keys(SHORTCUT_TOOL_BUTTONS).forEach((val) => {
      const {
        key, id,
      } = SHORTCUT_TOOL_BUTTONS[val];
      if ($(eventTarget).is(`#${id}`)) {
        buttonKey = key;
      }
    });
    if (!buttonKey) {
      Object.keys(SHORTCUT_MISC_BUTTONS).forEach((val) => {
        const {
          key, id,
        } = SHORTCUT_MISC_BUTTONS[val];
        if ($(eventTarget).is(`#${id}`)) {
          buttonKey = key;
        }
      });
    }
    this.buttons.forEach((button) => {
      if (button.buttonKey === buttonKey) {
        if (this.currentButton === button) {
          this.disableCurrentButton();
        } else {
          this.currentButton = button;
        }
      }
    });
    if (this.currentButton) {
      this.removeWaiting();
      $(`#${this.currentButton.buttonId}`).toggleClass('waiting');
    }
    $('body').on(
      {
        keydown: this.handleShortcutInput.bind(this),
      },
    );
  }

  restoreDefaultShortcuts() {
    this.keyboardService.restoreDefaultShortcuts();
    this.refreshShortcutButtons();
  }

  notifyChange(event) {
    this.data = event.data;
  }

  handleKeydownEvent(e) {
    const {
      key, ctrlKey, shiftKey, altKey,
    } = e;
    const eventValue = JSON.stringify({
      key: key.toUpperCase(), altKey, shiftKey, ctrlKey,
    });
    const property = this.keyboardService.checkShortcutValue(eventValue);
    if (property.length > 0) {
      e.preventDefault();
      const values = Object.values(KEYBOARD_SHORTCUTS_PROPERTIES_ENUM);
      if (values.indexOf(property[0][0]) > -1
      && property[0][0] === KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.OPEN_KB_SHORTCUTS) {
        $(`#${this.element.modalDialogId}`).dialog('open');
      }
    }
    return false;
  }

  listen() {
    $(`#${this.element.elementButtonId}`).on({
      click: () => {
        $(`#${this.element.modalDialogId}`).dialog('open');
      },
    });
    $('.shortcut-button').on({
      click: (e) => {
        this.handleShortcutButtonClick(e);
      },
    });
    $('#modal-dialog__restore-button').on({
      click: () => {
        this.restoreDefaultShortcuts();
      },
    });
    $('body').on({
      keydown: (event) => {
        if (!this.windowLocked) {
          this.handleKeydownEvent(event);
        }
      },
    });
    $(`#${this.element.modalDialogId}`).on({
      dialogclose: () => {
        $('#modal-dialog__error-message').html('');
        this.disableCurrentButton();
      },
    });
  }
}
