import $ from 'jquery';
import UPNG from 'upng-js';
// import FileSaver from 'file-saver';
import Pawskel from '../models/AppPawskel';
import ToolLayer from '../views/appTools/toolLayer/ToolLayer';
import GIF from '../../public/gif';

const BASE_COLOR = 'rgba(0, 0, 0, 0)';

export default class AppFilesaverService {
  constructor(frameService) {
    this.frameService = frameService;
    this.draftCanvas = null;
    this.val = 0;
  }

  createPawskel(numberofSquares, name) {
    return new Pawskel(numberofSquares, this.frameService.getFrames(), name);
  }

  static convertPawskelToPawskel(pawskel) {
    return new Blob([JSON.stringify(pawskel)], { type: 'application/octet-stream;charset=utf-8' });
  }

  renderDraftCanvas(numberOfSquares, width) {
    if (!this.draftCanvas) {
      this.draftCanvas = new ToolLayer(numberOfSquares, width, BASE_COLOR, -100);
      this.draftCanvas.render('grid-container');
      $(`#${this.draftCanvas.elementId}`).css({
        'z-index': -100,
        position: 'absolute',
      });
    }
  }

  convertPawskelToGif(numberOfSquares, width, rate) {
    this.renderDraftCanvas(numberOfSquares, width);
    const images = this.frameService.getImages();
    const gif = new GIF({
      workers: images.length * 2,
      width,
      height: width,
      transparent: true,
      workerScript: '../../public/gif.worker.js',
    });
    const canvas = document.getElementById(this.draftCanvas.elementId);
    const ctx = canvas.getContext('2d');
    for (let i = 0; i < images.length; i += 1) {
      const currentImage = images[i];
      currentImage.forEach((image) => {
        const drawingImage = new Image(width, width);
        drawingImage.src = image;
        ctx.drawImage(drawingImage, 0, 0);
      });
      gif.addFrame(ctx, { copy: true, delay: 1000 / rate });
      ctx.clearRect(0, 0, width, width);
    }
    return gif;
  }

  converPawskelToApng(numberOfSquares, width, rate) {
    this.renderDraftCanvas(numberOfSquares, width);
    const images = this.frameService.getImages();
    const data = [];
    const rateList = [];
    for (let i = 0; i < images.length; i += 1) {
      rateList.push(1000 / rate);
    }
    const canvas = document.getElementById(this.draftCanvas.elementId);
    const ctx = canvas.getContext('2d');
    for (let i = 0; i < images.length; i += 1) {
      const currentImage = images[i];
      currentImage.forEach((image) => {
        const drawingImage = new Image(width, width);
        drawingImage.src = image;
        ctx.drawImage(drawingImage, 0, 0);
      });
      const dta = ctx.getImageData(0, 0, width, width).data;
      data.push(dta.buffer);
      ctx.clearRect(0, 0, width, width);
    }
    return UPNG.encode(data, width, width, 0, rateList);
  }
}
