import $ from 'jquery';
import './frameComponentStyles.less';

export default class AppFrameComponentView {
  constructor() {
    this.id = 'frame-component';
    this.frameBarId = 'frame-bar';
    this.buttonId = 'frame-button';
    this.template = `<div class="${this.id}" id="${this.id}">
      <div class="frame-component__bar" id="${this.frameBarId}"></div>
      <button class="frame-component__button" id="${this.buttonId}">
        <i class="frame-component__button--icon icon-plus"></i>
        <span class="frame-component__button--span">Add new frame</span>
      </button>
    </div>`;
  }

  render(container) {
    $(`#${container}`).append(this.template);
  }
}
