import $ from 'jquery';
import firebase from 'firebase/app';
import 'firebase/auth';
import Header from '../views/appHeaderView/AppHeaderView';
import ISubject from '../services/ISubject';

const GITHUB_PROVIDER = 'GitHub';
const GOOGLE_PROVIDER = 'Google';

export default class AppHeaderController extends ISubject {
  constructor() {
    super();
    this.element = new Header();
    this.signedIn = false;
    this.provider = null;
    this.initialProvider = null;
    this.title = '';
  }

  init(container) {
    this.element.render(container);
    switch (this.initialProvider) {
      case GITHUB_PROVIDER:
        this.handleSignInWithGh();
        break;
      case GOOGLE_PROVIDER:
        this.handleSignInWithGoogle();
        break;
      default:
        break;
    }
  }

  notifyChange(event) {
    this.event = event;
  }

  updateFileTitle(name) {
    this.title = name;
    $('#header-title').html(`${this.title}`);
  }

  setInitialState(initialState) {
    this.signedIn = false;
    $('#user-details').html('');
    const { provider } = initialState;
    this.initialProvider = provider;
  }

  signIn(provider) {
    firebase.auth().signInWithPopup(provider).then((result) => {
      const { user } = result;
      $('#user-details').html(`Hello, ${user.displayName}!`);
    }).catch((error) => {
      const errorCode = error.code;
      const errorMessage = error.message;
      $('#user-details').html(`${errorCode}: ${errorMessage}`);
    });
    this.signedIn = true;
    $(`#${this.element.dialogOpenerId}`).html('Sign out');
  }

  signOut() {
    firebase.auth().signOut().then(() => {
      $('#user-details').html('');
    }).catch((error) => {
      const errorCode = error.code;
      const errorMessage = error.message;
      $('#user-details').html(`${errorCode}: ${errorMessage}`);
    });
    this.signedIn = false;
    $(`#${this.element.dialogOpenerId}`).html('Sign in');
  }

  handleSignInWithGoogle() {
    if (this.signedIn) {
      this.signOut();
      $(`#${this.element.googleSignInId} span`).html('Sign in with Google');
      this.provider = '';
    }
    if (this.provider !== GOOGLE_PROVIDER) {
      const provider = new firebase.auth.GoogleAuthProvider();
      this.signIn(provider);
      this.provider = GOOGLE_PROVIDER;
      $(`#${this.element.googleSignInId} span`).html('Sign out');
      $(`#${this.element.ghSignInId} span`).html('Sign in with GitHub');
    }
  }

  handleSignInWithGh() {
    if (this.signedIn) {
      this.signOut();
      $(`#${this.element.ghSignInId} span`).html('Sign in with GitHub');
      this.provider = '';
    }
    if (this.provider !== GITHUB_PROVIDER) {
      const provider = new firebase.auth.GithubAuthProvider();
      this.signIn(provider);
      this.provider = GITHUB_PROVIDER;
      $(`#${this.element.ghSignInId} span`).html('Sign out');
      $(`#${this.element.googleSignInId} span`).html('Sign in with Google');
    }
  }

  listen() {
    $(`#${this.element.googleSignInId}`).on({
      click: () => this.handleSignInWithGoogle(),
    });
    $(`#${this.element.ghSignInId}`).on({
      click: () => this.handleSignInWithGh(),
    });
    $(`#${this.element.dialogOpenerId}`).on({
      click: () => {
        $(`#${this.element.dialogId}`).dialog('open');
      },
    });
    $('#header__app-title').on({
      click: () => document.location.reload(),
    });
  }
}
