import $ from 'jquery';
import './toolLayerbuttonStyles.less';

export default class ToolLayerButton {
  constructor(number) {
    this.number = number;
    this.buttonId = `tool-layer-button-${number}`;
    this.template = `<button class="tool-layer-button" id="${this.buttonId}">
      Layer ${number}
    </button>`;
  }

  render(container) {
    $(`#${container}`).prepend(this.template);
  }
}
