import $ from 'jquery';
import './toolShortcutButtonStyles.less';

export default class ToolShortcutButton {
  constructor(key, id, label, text) {
    this.buttonKey = key;
    this.buttonId = id;
    this.label = label;
    this.currentlabel = label;
    this.text = text;
    this.template = `<div class="shortcut-info">
      <button class="shortcut-button" id="${this.buttonId}">${this.label}</button>
      <div class="shortcut-button__info">${this.text}</div>
    </div>`;
  }

  render(container) {
    $(container).append(this.template);
    $(`#${this.buttonId}`).button({
      label: `${this.label}`,
    });
  }
}
