import $ from 'jquery';
import AppframeComponentView from '../views/appFrameComponentView/AppFrameComponentView';
import ISubject from '../services/ISubject';
import ToolFrame from '../views/appTools/toolFrame/ToolFrame';
import Event, { EVENT_ENUM } from '../models/AppEvent';
import { MODEL_EVENT_ENUM } from '../models/AppModelEvent';
import AppToolTipService from '../services/AppToolTipService';
import { KEYBOARD_SHORTCUTS_PROPERTIES_ENUM } from '../models/AppKeyboardShortcutModel';
import { LAYER_EVENT_ENUM } from '../models/AppLayerEvent';

const WIDTH = 128;

export default class AppFrameController extends ISubject {
  constructor(baseColor, numberOfSquares, keyboardService) {
    super();
    this.element = new AppframeComponentView();
    this.currentFrame = null;
    this.activeFrame = null;
    this.baseColor = baseColor;
    this.numberOfSquares = numberOfSquares;
    this.activeFrameIndex = 0;
    this.deletedIndex = 0;
    this.duplicateIndex = 0;
    this.currentNumber = 0;
    this.frames = [];
    this.keyboardService = keyboardService;
    this.windowLocked = false;
  }

  scrollCurrentFrameIntoView() {
    $(`#${this.currentFrame.number}`)[0].scrollIntoView({
      behavior: 'smooth',
      block: 'center',
    });
  }

  addEmptyFrame() {
    this.currentFrame = new ToolFrame(WIDTH, this.currentNumber + 1);
    this.currentFrame.render(`#${this.element.frameBarId}`);
    $(`#${this.currentFrame.number}`).addClass('active');
    this.removeActive();
    this.frames.push(this.currentFrame);
    this.activeFrame = this.currentFrame;
    this.currentNumber += 1;
    this.scrollCurrentFrameIntoView();
  }

  init(container) {
    this.element.render(container);
    if (this.frames.length === 0) {
      this.addEmptyFrame();
    }
    $(`#${this.element.frameBarId}`).sortable({
      axis: 'y',
      distance: 10,
      containment: 'parent',
      cursor: 'move',
      tolerance: 'pointer',
      forcePlaceholderSize: true,
      placeholder: 'sortable-placeholder',
    });
    AppToolTipService.addToolTipByClass('delete', 'button', 'Delete this frame');
    AppToolTipService.addToolTipByClass('duplicate', 'button', 'Duplicate this frame');
  }

  setInitialState(initialState) {
    const { numberOfSquares } = initialState;
    this.numberOfSquares = numberOfSquares;
  }

  removeActive() {
    for (let i = 0; i < this.frames.length; i += 1) {
      const elem = this.frames[i];
      if (elem !== this.currentFrame) {
        $(`#${elem.number}`).removeClass('active');
      }
    }
  }

  refreshFrameNumbers(val) {
    for (let i = 0; i < this.frames.length; i += 1) {
      const elem = this.frames[i];
      elem.refreshId(i + val);
    }
  }

  changeFramesOrder(sortedIds) {
    const changedFrames = [];
    sortedIds.forEach((val) => {
      changedFrames.push(this.frames[val - 1]);
    });
    this.frames = changedFrames;
    this.refreshFrameNumbers(this.frames.length);
    this.refreshFrameNumbers(0);
  }

  drawSingleFrame(frame, mode) {
    const squareWidth = WIDTH / this.numberOfSquares;
    const canvas = document.getElementById(`canvas-${this.currentFrame.number}`);
    if (canvas) {
      const ctx = canvas.getContext('2d');
      ctx.globalCompositeOperation = mode;
      frame.forEach((row, i) => {
        row.forEach((column, j) => {
          ctx.fillStyle = column;
          ctx.fillRect(i * squareWidth, j * squareWidth, squareWidth, squareWidth);
        });
      });
    }
  }

  drawFrame(frame) {
    this.drawSingleFrame(frame, 'source-over');
  }

  refreshCurrentFrame(frame) {
    this.drawSingleFrame(frame, 'copy');
  }

  clearFrames() {
    this.frames.forEach(frame => frame.clear());
  }

  addDuplicateFrame(frame) {
    const prevIndex = frame.number;
    this.addEmptyFrame();
    $(`#${this.currentFrame.number}`).insertAfter(`#${frame.number}`);
    this.frames.pop();
    this.frames.splice(prevIndex, 0, this.currentFrame);
    for (let i = prevIndex + 1; i < this.frames.length; i += 1) {
      this.frames[i].refreshId(i + this.frames.length);
    }
    this.currentFrame.refreshId(prevIndex);
    this.refreshFrameNumbers(0);
    this.scrollCurrentFrameIntoView();
  }

  drawCurrentLayer(layers) {
    for (let i = 0; i < this.frames.length; i += 1) {
      const frame = this.frames[i];
      frame.clear();
      frame.drawFrame(layers[i], this.numberOfSquares);
    }
  }

  drawInitialFrames(eventData) {
    const {
      currentFrameIndex,
      numberOfFrames,
      currentLayers,
    } = eventData;
    this.numberOfSquares = currentLayers[0].length;
    if (this.frames.length > numberOfFrames) {
      let index = this.frames.length - 1;
      while (index >= numberOfFrames) {
        $(`#${this.frames[index].number}`).remove();
        this.frames.pop();
        index -= 1;
      }
    } else if (this.frames.length < numberOfFrames) {
      for (let i = this.frames.length; i < numberOfFrames; i += 1) {
        this.addEmptyFrame();
      }
    }
    this.currentFrame = this.frames[currentFrameIndex];
    this.currentNumber = this.frames.length;
    this.drawCurrentLayer(currentLayers);
    this.removeActive();
    this.refreshFrameNumbers(0);
    $(`#${this.currentFrame.number}`).addClass('active');
  }

  refreshCurrentLayers(event) {
    const { currentLayers } = event.data;
    this.drawCurrentLayer(currentLayers);
  }

  resizeFrames(eventData) {
    const { value, currentLayers } = eventData;
    this.numberOfSquares = value;
    this.drawCurrentLayer(currentLayers);
  }

  notifyChange(event) {
    if (event.type === MODEL_EVENT_ENUM.FRAME_DUPLICATE
      || event.type === MODEL_EVENT_ENUM.FRAME_CHANGE) {
      this.drawFrame(event.data);
    }
    if (event.type === MODEL_EVENT_ENUM.ERASE_CANVAS
      || event.type === MODEL_EVENT_ENUM.FRAME_UPDATE) {
      this.refreshCurrentFrame(event.data);
      this.drawFrame(event.data);
    }
    if (event.type === LAYER_EVENT_ENUM.ADD_LAYER) {
      this.clearFrames();
    }
    if (event.type === LAYER_EVENT_ENUM.REMOVE_LAYER
      || event.type === MODEL_EVENT_ENUM.CURRENT_LAYER_CHANGE) {
      this.refreshCurrentLayers(event);
    }
    if (event.type === MODEL_EVENT_ENUM.CANVAS_RESIZE) {
      this.resizeFrames(event.data);
    }
    if (event.type === MODEL_EVENT_ENUM.SET_INITIAL_STATE) {
      this.drawInitialFrames(event.data);
    }
  }

  handleFrameDeletion(event) {
    const target = $(event.target);
    const number = +$(target).parent().parent().attr('id');
    const frameToDelete = this.frames[number - 1];
    if (this.frames.length > 1) {
      if (frameToDelete === this.currentFrame) {
        if (this.frames.indexOf(this.currentFrame) > 0) {
          this.currentFrame = this.frames[number - 2];
        } else {
          const frame = this.frames[1];
          this.currentFrame = frame;
        }
        $(`#${this.currentFrame.number}`).addClass('active');
      }
      this.frames.splice(number - 1, 1);
      $(target).parent().parent().remove();
      this.refreshFrameNumbers(0);
      this.scrollCurrentFrameIntoView();
      this.currentNumber -= 1;
      this.deletedIndex = number - 1;
      $(`#${this.element.frameBarId}`).sortable('refresh');
      const e = new Event(EVENT_ENUM.DELETE_CLICK, event);
      this.notify(e);
    }
  }

  selectFrame(index) {
    if (index >= 0 && index < this.frames.length) {
      this.currentFrame = this.frames[index];
      this.scrollCurrentFrameIntoView();
      $(`#${this.currentFrame.number}`).addClass('active');
      this.removeActive();
      const e = new Event(EVENT_ENUM.FRAME_CANVAS_CLICK);
      this.notify(e);
    }
  }

  handleKeyPress(event) {
    const {
      key, ctrlKey, shiftKey, altKey,
    } = event;
    const eventValue = JSON.stringify({
      key: key.toUpperCase(), altKey, shiftKey, ctrlKey,
    });
    const property = this.keyboardService.checkShortcutValue(eventValue);
    if (property.length > 0) {
      event.preventDefault();
      const values = Object.values(KEYBOARD_SHORTCUTS_PROPERTIES_ENUM);
      if (values.indexOf(property[0][0]) > -1) {
        switch (property[0][0]) {
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.NEW_FRAME:
            this.addEmptyFrame();
            this.notify(new Event(EVENT_ENUM.FRAME_BUTTONCLICK, event));
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.DUPLICATE_SEL_FRAME:
            this.duplicateIndex = this.frames.indexOf(this.currentFrame);
            this.addDuplicateFrame(this.currentFrame);
            this.notify(new Event(EVENT_ENUM.DUPLICATE_CLICK, event));
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.SELECT_PREV_FRAME:
            this.selectFrame(this.frames.indexOf(this.currentFrame) - 1);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.SELECT_NEXT_FRAME:
            this.selectFrame(this.frames.indexOf(this.currentFrame) + 1);
            break;
          default:
            break;
        }
      }
    }
    return false;
  }

  listen() {
    $(`#${this.element.id}`).on({
      click: (event) => {
        const target = $(event.target);
        if (target.is(`#${this.element.buttonId}`) || target.is('.frame-component__button--icon')
        || target.is('.frame-component__button--span')) {
          this.addEmptyFrame();
          const e = new Event(EVENT_ENUM.FRAME_BUTTONCLICK, event);
          this.notify(e);
        }
        if (target.is('.frame-canvas')) {
          const number = +$(target).parent().attr('id');
          this.currentFrame = this.frames[number - 1];
          this.scrollCurrentFrameIntoView();
          $(`#${this.currentFrame.number}`).addClass('active');
          this.removeActive();
          const e = new Event(EVENT_ENUM.FRAME_CANVAS_CLICK, event);
          this.notify(e);
        }
        if ($(target).parent().is('.delete')) {
          this.handleFrameDeletion(event);
        }
        if ($(target).parent().is('.duplicate')) {
          const number = +$(target).parent().parent().attr('id');
          const frameToDuplicate = this.frames[number - 1];
          this.duplicateIndex = number - 1;
          this.addDuplicateFrame(frameToDuplicate);
          $(`#${this.element.frameBarId}`).sortable('refresh');
          const e = new Event(EVENT_ENUM.DUPLICATE_CLICK, event);
          this.notify(e);
        }
      },
    });
    $(`#${this.element.frameBarId}`).on({
      sortupdate: () => {
        const sortedIDs = $(`#${this.element.frameBarId}`).sortable('toArray');
        this.changeFramesOrder(sortedIDs);
        const event = new Event(EVENT_ENUM.FRAME_SORT, sortedIDs);
        this.notify(event);
      },
    });
    $('body').on({
      keydown: (e) => {
        if (!this.windowLocked) {
          e.preventDefault();
          this.handleKeyPress(e);
        }
      },
    });
  }
}
