import $ from 'jquery';
import './appModalDialogStyles.less';

export default class AppModalDialogView {
  constructor() {
    this.elementId = 'modal-dialog';
    this.elementButtonId = 'modal-dialog__opener';
    this.modalDialogId = 'modal-dialog__dialog-window';
    this.template = `<div class="${this.elementId}" id="${this.elementId}">
      <button class="${this.elementButtonId}" id="${this.elementButtonId}">
        <i class="modal-dialog__opener--icon icon-keyboard"></i>
      </button>
      <div class="${this.modalDialogId}" id="${this.modalDialogId}" title="Keyboard Shortcuts">
        <h2 class="modal-dialog__section-header">Tool shortcuts</h2>
        <div class="modal-dialog__fieldset" id="modal-dialog__fieldset--tools"></div>
        <h2 class="modal-dialog__section-header">Misc shortcuts</h2>
        <div class="modal-dialog__fieldset" id="modal-dialog__fieldset--misc"></div>
        <button class="modal-dialog__restore-button" id="modal-dialog__restore-button">
          Restore default shortcuts
        </button>
        <div class="modal-dialog__error-message" id ="modal-dialog__error-message"></div>
      </div>
    </div>`;
  }

  render(container) {
    $(`#${container}`).append(this.template);
    $(`#${this.modalDialogId}`).dialog({
      autoOpen: false,
      show: { effect: 'fold', duration: 1000 },
      hide: { effect: 'fold', duration: 1000 },
      modal: true,
      minWidth: 1000,
      minHeight: 800,
    });
  }
}
