import AppKeyboardShortcutModel from '../models/AppKeyboardShortcutModel';

export default class AppKeyboardShortcutService {
  constructor() {
    this.keyboardShortcutModel = new AppKeyboardShortcutModel();
  }

  setShortcuts(shortCuts) {
    this.keyboardShortcutModel.setShortcuts(shortCuts);
  }

  init() {
    if (Object.keys(this.keyboardShortcutModel.shortcuts).length === 0) {
      this.keyboardShortcutModel.setDefaultState();
    }
  }

  getShortCuts() {
    return this.keyboardShortcutModel.shortcuts;
  }

  setNewShortcutValue(property, shortcut) {
    if (this.keyboardShortcutModel.shortcuts[property]) {
      const currentShortcutValue = this.keyboardShortcutModel.shortcuts[property];
      if (shortcut === currentShortcutValue) {
        return { changed: false, property };
      }
      const modelProperties = Object.keys(this.keyboardShortcutModel.shortcuts);
      for (let i = 0; i < modelProperties.length; i += 1) {
        const currProperty = modelProperties[i];
        const currShortcutValue = this.keyboardShortcutModel.shortcuts[currProperty];
        if (currShortcutValue === shortcut) {
          return { changed: false, property: currProperty };
        }
      }
      this.keyboardShortcutModel.shortcuts[property] = shortcut;
      return { changed: true, property };
    }
    return { changed: false, property: 'undefined' };
  }

  getShortcutValue(property) {
    return JSON.parse(this.keyboardShortcutModel.shortcuts[property]);
  }

  checkShortcutValue(eventValue) {
    const shortcutEntries = Object.entries(this.keyboardShortcutModel.shortcuts)
      .filter(entry => entry[1] === eventValue);
    return shortcutEntries;
  }

  restoreDefaultShortcuts() {
    this.setShortcuts();
  }
}
