import $ from 'jquery';
import './headerStyles.less';

export default class AppHeaderView {
  constructor() {
    this.dialogId = 'sign-in__dialog';
    this.dialogOpenerId = 'header__button--sign_in';
    this.googleSignInId = 'dialog__google-sign-in';
    this.ghSignInId = 'dialog__gh-sign-in';
    this.template = `<header class="header">
      <a class="header__app-title-link" id="header__app-title" href="#"><h1 class="header__title">Pawskel</h1></a>
      <div class="header-img"></div>
      <h2 class="header__title" id="header-title">New Pawskel</h2>
      <p class="user-details" id="user-details"></p>
      <button class="header__button" id="${this.dialogOpenerId}">Sign In</button>
      <div class="${this.dialogId}" id="${this.dialogId}" title="Sign In">
        <button class="dialog__button" id="${this.googleSignInId}">
          <i class="dialog__button-icon icon-google"></i>
          <span>Sign in with Google</span>
        </button>
        <button class="dialog__button" id="${this.ghSignInId}">
          <i class="dialog__button-icon icon-github-circled"></i>
          <span>Sign in with GitHub</span>
        </button>
      </div>
    </header>`;
  }

  render(container) {
    $(`#${container}`).append(this.template);
    $(`#${this.dialogId}`).dialog({
      autoOpen: false,
      show: { effect: 'fold', duration: 1000 },
      hide: { effect: 'fold', duration: 1000 },
      modal: true,
      width: 300,
      height: 250,
    });
  }
}
