
import ToolCanvasAvatar from '../toolCanvasAvatar/ToolCanvasAvatar';
import './toolLayerStyles.less';

export default class ToolLayer extends ToolCanvasAvatar {
  constructor(size, width, color, number) {
    super(size, width, color);
    this.number = number;
    this.elementId = `canvas-layer-${number}`;
    this.template = `<canvas class="canvas-layer" id="${this.elementId}" width="${this.width}px" height="${this.width}px"
    style="background-color: ${this.baseColor};">`;
  }

  clear() {
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    ctx.clearRect(0, 0, this.width, this.width);
  }
}
