import $ from 'jquery';
import AppCanvasView from '../../appCanvasView/AppCanvasView';
import './toolCanvasAvatarStyles.less';

const HIGHLIGHT_COLOR = 'rgba(240, 233, 158, 0.3)';

export default class ToolCanvasAvatar extends AppCanvasView {
  constructor(size, width, color) {
    super(size, width, color);
    this.elementId = 'canvas-avatar';
    this.class = 'canvas-avatar';
    this.template = `<canvas class="${this.class}" id="${this.elementId}" width="${this.width}px" height="${this.width}px"
    style="background-color: ${this.baseColor};">`;
    this.locked = false;
    this.shapeLocked = false;
    this.initialCoords = {};
    this.marginLeft = 0;
    this.marginTop = 0;
    this.highlightColor = HIGHLIGHT_COLOR;
    this.shapeApplied = false;
  }

  clear() {
    $(`#${this.elementId}`).remove();
    this.locked = false;
    this.initialCoords = {};
    this.marginLeft = 0;
    this.marginTop = 0;
  }

  clearArea(x, y) {
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    if (x >= 0) {
      if (y >= 0) {
        ctx.clearRect(this.width - x, 0, x, this.width);
        ctx.clearRect(0, this.width - y, this.width - x, y);
      } else {
        ctx.clearRect(0, 0, this.width, -y);
        ctx.clearRect(this.width - x, -y, x, this.width + y);
      }
    } else if (y >= 0) {
      ctx.clearRect(0, 0, -x, this.width);
      ctx.clearRect(this.width + x, this.width - y, this.width + x, y);
    } else {
      ctx.clearRect(0, 0, -x, this.width);
      ctx.clearRect(this.width + x, 0, this.width + x, -y);
    }
  }

  drawShape(data) {
    const { color, squaresList } = data;
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    squaresList.forEach((val) => {
      const [x, y] = val;
      ctx.fillStyle = color;
      ctx.fillRect(x * this.squareWidth, y * this.squareWidth,
        this.squareWidth, this.squareWidth);
      ctx.fillStyle = this.highlightColor;
      ctx.fillRect(x * this.squareWidth, y * this.squareWidth,
        this.squareWidth, this.squareWidth);
    });
  }
}
