import $ from 'jquery';
import ISubject from '../services/ISubject';
import AppColorPickerView from '../views/appColorPickerView/AppColorPickerView';
import Event, { EVENT_ENUM } from '../models/AppEvent';
import { MODEL_EVENT_ENUM } from '../models/AppModelEvent';
import AppToolTipService from '../services/AppToolTipService';

export default class AppColorPickerController extends ISubject {
  constructor(primaryColor, secondaryColor) {
    super();
    this.element = null;
    this.primarycolor = primaryColor;
    this.secondaryColor = secondaryColor;
  }

  init(container) {
    this.element = new AppColorPickerView(this.primaryColor, this.secondaryColor);
    this.element.render(container);
    AppToolTipService.addToolTipByClass('color-picker__input--primary', 'div', 'Primary. Left button');
    AppToolTipService.addToolTipByClass('color-picker__input--secondary', 'div', 'Secondary. Right button');
  }

  setInitialState(initialState) {
    const {
      primaryColor,
      secondaryColor,
    } = initialState;
    this.primaryColor = primaryColor;
    this.secondaryColor = secondaryColor;
  }

  notifyChange(event) {
    if (event.type === MODEL_EVENT_ENUM.FRAME_ADD
      || event.type === MODEL_EVENT_ENUM.CURRENT_FRAME_CHANGE
      || event.type === MODEL_EVENT_ENUM.FRAME_DUPLICATE) {
      this.listen();
    }
    if (event.type === MODEL_EVENT_ENUM.SET_PRIMARY_COLOR) {
      this.primaryColor = event.data;
      $(`#${this.element.primaryColorId}`).spectrum('set', this.primaryColor);
      const e = new Event(EVENT_ENUM.PRIMARY_COLOR_CHANGE, event);
      this.notify(e);
    }
    if (event.type === MODEL_EVENT_ENUM.SET_SECONDARY_COLOR) {
      this.secondaryColor = event.data;
      $(`#${this.element.secondaryColorId}`).spectrum('set', this.secondaryColor);
      const e = new Event(EVENT_ENUM.SECONDARY_COLOR_CHANGE, event);
      this.notify(e);
    }
  }

  listen() {
    $(`#${this.element.primaryColorId}`).on({
      change: (event) => {
        this.primaryColor = event.target.value;
        const e = new Event(EVENT_ENUM.PRIMARY_COLOR_CHANGE, event);
        this.notify(e);
      },
    });
    $(`#${this.element.secondaryColorId}`).on({
      change: (event) => {
        this.secondaryColor = event.target.value;
        const e = new Event(EVENT_ENUM.SECONDARY_COLOR_CHANGE, event);
        this.notify(e);
      },
    });
    $('#canvas-main__container').on('mousedown', (event) => {
      if (event.which === 3) {
        const colorValue = $(`#${this.element.secondaryColorId}`).spectrum('get');
        $(`#${this.element.secondaryColorId}`).spectrum('set', colorValue.toHexString());
        $(`#${this.element.secondaryColorId}`).spectrum('hide');
        this.secondaryColor = colorValue.toHexString();
        const e = new Event(EVENT_ENUM.SECONDARY_COLOR_CHANGE);
        this.notify(e);
      }
      if (event.which === 1) {
        const colorValue = $(`#${this.element.primaryColorId}`).spectrum('get');
        $(`#${this.element.primaryColorId}`).spectrum('set', colorValue.toHexString());
        $(`#${this.element.primaryColorId}`).spectrum('hide');
        this.primaryColor = colorValue.toHexString();
        const e = new Event(EVENT_ENUM.PRIMARY_COLOR_CHANGE);
        this.notify(e);
      }
    });
  }
}
