import $ from 'jquery';
import './colorPickerStyles.less';
import 'spectrum-colorpicker';
import './spectrum.css';

export default class AppColorPickerView {
  constructor(primaryColor, secondaryColor) {
    this.primaryColor = primaryColor;
    this.secondaryColor = secondaryColor;
    this.elementId = 'color-picker';
    this.primaryColorId = 'color-picker__button--primary';
    this.secondaryColorId = 'color-picker__button--secondary';
    this.template = `<div class="${this.elementId}" id="${this.elementId}">
      <input class="color-picker__input ${this.primaryColorId}" id="${this.primaryColorId}" type="text" value="${this.primaryColor}"></input>
      <input class="color-picker__input ${this.secondaryColorId}" id="${this.secondaryColorId}" type="text" value="${this.secondaryColor}"></input>
    </div>`;
  }

  render(container) {
    $(`#${container}`).append(this.template);
    this.init();
  }

  init() {
    $(`#${this.primaryColorId}`).spectrum({
      color: `${this.primaryColor}`,
      containerClassName: 'color-picker__input-container',
      replacerClassName: 'color-picker__input color-picker__input--primary',
      preferredFormat: 'hex',
      showInput: true,
      showPalette: true,
      showSelectionPalette: true,
      hideAfterPaletteSelect: true,
      clickoutFiresChange: true,
      showButtons: false,
      maxSelectionSize: 8,
      palette: [
        [`${this.primaryColor}`],
      ],
    });
    $(`#${this.secondaryColorId}`).spectrum({
      color: `${this.secondaryColor}`,
      containerClassName: 'color-picker__input-container',
      replacerClassName: 'color-picker__input color-picker__input--secondary',
      showInput: true,
      preferredFormat: 'hex',
      showPalette: true,
      showSelectionPalette: true,
      hideAfterPaletteSelect: true,
      clickoutFiresChange: true,
      showButtons: false,
      maxSelectionSize: 8,
      palette: [
        [`${this.secondaryColor}`],
      ],
    });
  }
}
