export const LAYER_EVENT_ENUM = {
  ADD_LAYER: 'addLayer',
  REMOVE_LAYER: 'deletelayer',
  MOVE_LAYER_UP: 'moveLayerUp',
  MOVE_LAYER_DOWN: 'moveLayerDown',
  CHANGE_CURRENT_LAYER: 'changeCurrentLayer',
};

export default class AppLayerEvent {
  constructor(type, data) {
    this.type = type;
    this.data = data;
  }
}
