export default class AppColorConverter {
  static convertRgbToHsl(R, G, B) {
    let r = R;
    let g = G;
    let b = B;
    r /= 255;
    g /= 255;
    b /= 255;
    const max = Math.max(r, g, b);
    const min = Math.min(r, g, b);
    let h = 0;
    let s = 0;
    let l = (max + min) / 2;
    const delta = max - min;
    if (delta === 0) {
      h = 0;
      s = 0;
    } else if (max === r) {
      h = ((g - b) / delta) % 6;
    } else if (max === g) {
      h = (b - r) / delta + 2;
    } else {
      h = (r - g) / delta + 4;
    }
    h = Math.round(h * 60);
    if (h < 0) {
      h += 360;
    }
    if (delta !== 0) {
      s = delta / (1 - Math.abs(2 * l - 1));
    }
    s = +(s * 100).toFixed(1);
    l = +(l * 100).toFixed(1);
    return { h, s, l };
  }

  static convertHslToRgb(color) {
    const { h } = color;
    let { s, l } = color;
    s /= 100;
    l /= 100;
    let r;
    let g;
    let b;
    const chroma = (1 - Math.abs(2 * l - 1)) * s;
    const x = chroma * (1 - Math.abs(((h / 60) % 2) - 1));
    const matchLightness = l - chroma / 2;
    if (h >= 0 && h < 60) {
      r = chroma; g = x; b = 0;
    } else if (h >= 60 && h < 120) {
      r = x; g = chroma; b = 0;
    } else if (h >= 120 && h < 180) {
      r = 0; g = chroma; b = x;
    } else if (h >= 180 && h < 240) {
      r = 0; g = x; b = chroma;
    } else if (h >= 240 && h < 300) {
      r = x; g = 0; b = chroma;
    } else if (h >= 300 && h < 360) {
      r = chroma; g = 0; b = x;
    }
    r = Math.round((r + matchLightness) * 255);
    g = Math.round((g + matchLightness) * 255);
    b = Math.round((b + matchLightness) * 255);
    return { r, g, b };
  }

  static changeColor(colorHex, lightness) {
    const R = parseInt(colorHex.substring(1, 3), 16);
    const G = parseInt(colorHex.substring(3, 5), 16);
    const B = parseInt(colorHex.substring(5, 7), 16);
    const colorHSL = AppColorConverter.convertRgbToHsl(R, G, B);
    const lightSign = Math.sign(lightness);
    switch (lightSign) {
      case 1:
        if (colorHSL.l <= 100 - lightness) {
          colorHSL.l += lightness;
        }
        break;
      case -1:
        if (colorHSL.l >= Math.abs(lightness)) {
          colorHSL.l += lightness;
        }
        break;
      default:
        break;
    }
    const finalRGB = AppColorConverter.convertHslToRgb(colorHSL);
    const { r, g, b } = finalRGB;
    const finalR = ((r.toString(16).length === 1) ? `0${r.toString(16)}` : r.toString(16));
    const finalG = ((g.toString(16).length === 1) ? `0${g.toString(16)}` : g.toString(16));
    const finalB = ((b.toString(16).length === 1) ? `0${b.toString(16)}` : b.toString(16));
    return `#${finalR}${finalG}${finalB}`;
  }
}
