import $ from 'jquery';
import ISubject from '../services/ISubject';
import AppViewToolBar, { BUTTON_ENUM } from '../views/appToolBarView/AppToolBarView';
import Event, { EVENT_ENUM } from '../models/AppEvent';
import AppToolTipService from '../services/AppToolTipService';
import { KEYBOARD_SHORTCUTS_PROPERTIES_ENUM } from '../models/AppKeyboardShortcutModel';

const ACTIVE_SIZE_BUTTON = {
  1: '#tool-bar__size-buttons--button_one',
  2: '#tool-bar__size-buttons--button_two',
  3: '#tool-bar__size-buttons--button_three',
  4: '#tool-bar__size-buttons--button_four',
};
const DEFAULT_TOOL = BUTTON_ENUM.PEN;
const DEFAULT_PENSIZE = 1;

export default class ToolBarController extends ISubject {
  constructor(keyboardService) {
    super();
    this.currentTool = DEFAULT_TOOL;
    this.element = new AppViewToolBar();
    this.currentPenSize = DEFAULT_PENSIZE;
    this.keyboardService = keyboardService;
    this.activeSizeButton = ACTIVE_SIZE_BUTTON[DEFAULT_PENSIZE];
    this.windowLocked = false;
  }

  init(container) {
    this.element.render(container);
    $(this.activeSizeButton).addClass('active');
    $(`#${this.currentTool}`).addClass('active');
    AppToolTipService.addToolTips();
    AppToolTipService.addToolTipById('tool-bar__size-buttons--button_one', 'div', '1x');
    AppToolTipService.addToolTipById('tool-bar__size-buttons--button_two', 'div', '2x');
    AppToolTipService.addToolTipById('tool-bar__size-buttons--button_three', 'div', '3x');
    AppToolTipService.addToolTipById('tool-bar__size-buttons--button_four', 'div', '4x');
  }

  setInitialState(currentState) {
    const {
      currentTool,
      penSize,
    } = currentState;
    if (currentTool === BUTTON_ENUM.MOVE
      || currentTool === BUTTON_ENUM.SHAPE_SELECTION) {
      this.currentTool = BUTTON_ENUM.PEN;
    } else {
      this.currentTool = currentTool;
    }
    this.currentPenSize = penSize;
    this.activeSizeButton = ACTIVE_SIZE_BUTTON[this.currentPenSize];
  }

  setCurrentToolOnKeyboardEvent(toolName) {
    if (toolName !== this.currentTool) {
      this.currentTool = toolName;
      ToolBarController.removeActive(this.element.toolBarId);
      $(`#${this.currentTool}`).addClass('active');
    }
  }

  setCurrentTool(toolName, e) {
    this.currentTool = toolName;
    ToolBarController.removeActive(this.element.toolBarId);
    $(e.target).parent().addClass('active');
  }

  notifyChange(event) {
    this.frame = event.data;
  }

  static removeActive(elementId) {
    const children = $(`#${elementId}`).children();
    for (let i = 0; i < children.length; i += 1) {
      $(children[i]).removeClass('active');
    }
  }

  setCurrentParentPenSize(e) {
    ToolBarController.removeActive(this.element.toolBarSizeId);
    $(e.target).parent().addClass('active');
    if ($(e.target).parent().is('#tool-bar__size-buttons--button_one')) {
      this.currentPenSize = 1;
    } else if ($(e.target).parent().is('#tool-bar__size-buttons--button_two')) {
      this.currentPenSize = 2;
    } else if ($(e.target).parent().is('#tool-bar__size-buttons--button_three')) {
      this.currentPenSize = 3;
    } else {
      this.currentPenSize = 4;
    }
  }

  setCurrentPenSize(e) {
    ToolBarController.removeActive(this.element.toolBarSizeId);
    $(e.target).addClass('active');
    if ($(e.target).is('#tool-bar__size-buttons--button_one')) {
      this.currentPenSize = 1;
    } else if ($(e.target).is('#tool-bar__size-buttons--button_two')) {
      this.currentPenSize = 2;
    } else if ($(e.target).is('#tool-bar__size-buttons--button_three')) {
      this.currentPenSize = 3;
    } else {
      this.currentPenSize = 4;
    }
  }

  changePenSize(size) {
    if (size <= 4 && size > 0) {
      this.currentPenSize = size;
      ToolBarController.removeActive(this.element.toolBarSizeId);
      switch (this.currentPenSize) {
        case 1:
          $('#tool-bar__size-buttons--button_one').addClass('active');
          break;
        case 2:
          $('#tool-bar__size-buttons--button_two').addClass('active');
          break;
        case 3:
          $('#tool-bar__size-buttons--button_three').addClass('active');
          break;
        default:
          $('#tool-bar__size-buttons--button_four').addClass('active');
          break;
      }
      const event = new Event(EVENT_ENUM.PEN_SIZE_CHANGE);
      this.notify(event);
    }
  }

  handleKeyPress(e) {
    const {
      key, ctrlKey, shiftKey, altKey,
    } = e;
    const eventValue = JSON.stringify({
      key: key.toUpperCase(), altKey, shiftKey, ctrlKey,
    });
    const property = this.keyboardService.checkShortcutValue(eventValue);
    if (property.length > 0) {
      e.preventDefault();
      const values = Object.values(KEYBOARD_SHORTCUTS_PROPERTIES_ENUM);
      if (values.indexOf(property[0][0]) > -1) {
        switch (property[0][0]) {
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.INCREASE_PEN_SIZE:
            this.changePenSize(this.currentPenSize + 1);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.DECREASE_PEN_SIZE:
            this.changePenSize(this.currentPenSize - 1);
            break;
          default:
            break;
        }
      }
    }
    return false;
  }

  listen() {
    $(`#${this.element.elementId}`).on('click', (e) => {
      if ($(e.target).parent().is(`#${BUTTON_ENUM.COLOR_PICKER}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.COLOR_PICKER, e);
        const event = new Event(EVENT_ENUM.COLOR_PICKER_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.PEN}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.PEN, e);
        const event = new Event(EVENT_ENUM.PEN_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.ERASER}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.ERASER, e);
        const event = new Event(EVENT_ENUM.ERASER_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.PAINT_ALL_BUCKET}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.PAINT_ALL_BUCKET, e);
        const event = new Event(EVENT_ENUM.PAINT_ALL_BUCKET_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.MIRROR_PEN}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.MIRROR_PEN, e);
        const event = new Event(EVENT_ENUM.MIRROR_PEN_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.PAINT_BUCKET}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.PAINT_BUCKET, e);
        const event = new Event(EVENT_ENUM.PAINT_BUCKET_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.STROKE}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.STROKE, e);
        const event = new Event(EVENT_ENUM.STROKE_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.MOVE}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.MOVE, e);
        const event = new Event(EVENT_ENUM.MOVE_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.RECTANGLE}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.RECTANGLE, e);
        const event = new Event(EVENT_ENUM.RECTANGLE_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.CIRCLE}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.CIRCLE, e);
        const event = new Event(EVENT_ENUM.CIRCLE_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.LIGHTEN}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.LIGHTEN, e);
        const event = new Event(EVENT_ENUM.LIGHTEN_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.DITHERING}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.DITHERING, e);
        const event = new Event(EVENT_ENUM.DITHERING_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.SHAPE_SELECTION}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.SHAPE_SELECTION, e);
        const event = new Event(EVENT_ENUM.SHAPE_SELECTION_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.ROTATE}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.ROTATE, e);
        const event = new Event(EVENT_ENUM.ROTATE_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).parent().is(`#${BUTTON_ENUM.FLIP}`)) {
        e.stopPropagation();
        this.setCurrentTool(BUTTON_ENUM.FLIP, e);
        const event = new Event(EVENT_ENUM.FLIP_BUTTON_CLICK, e);
        this.notify(event);
      }
      if ($(e.target).is('.tool-bar__size-buttons--button_inner')) {
        e.stopPropagation();
        this.setCurrentParentPenSize(e);
        const event = new Event(EVENT_ENUM.PEN_SIZE_CHANGE);
        this.notify(event);
      }
      if ($(e.target).is('.tool-bar__size-buttons--button_outer')) {
        e.stopPropagation();
        this.setCurrentPenSize(e);
        const event = new Event(EVENT_ENUM.PEN_SIZE_CHANGE);
        this.notify(event);
      }
    });
    $('body').on({
      keydown: (e) => {
        if (!this.windowLocked) {
          this.handleKeyPress(e);
        }
      },
    });
  }
}
