import $ from 'jquery';
import ToolButton from '../appTools/toolButton/ToolButton';
import './toolBarStyles.less';

export const BUTTON_ENUM = {
  PEN: 'pen',
  MIRROR_PEN: 'mirrorPen',
  PAINT_BUCKET: 'paint-bucket',
  PAINT_ALL_BUCKET: 'paint-all-bucket',
  ERASER: 'eraser',
  STROKE: 'stroke',
  RECTANGLE: 'rectangle',
  CIRCLE: 'circle',
  MOVE: 'move',
  FLIP: 'flip',
  ROTATE: 'rotate',
  SHAPE_SELECTION: 'shape-selection',
  LIGHTEN: 'lighten',
  DITHERING: 'dithering',
  COLOR_PICKER: 'color-picker',
};

export default class AppViewToolBar {
  constructor() {
    this.elementId = 'palette';
    this.toolBarId = 'tool-bar';
    this.toolBarSizeId = 'tool-bar__size-buttons';
    this.template = `<div class="palette" id="${this.elementId}">
      <div class="tool-bar__size-buttons" id="${this.toolBarSizeId}">
        <div class="tool-bar__size-buttons--button_outer" id="tool-bar__size-buttons--button_one">
          <div class="tool-bar__size-buttons--button_inner tool-bar__size-buttons--button_one_inner"></div>
        </div>
      <div class="tool-bar__size-buttons--button_outer" id="tool-bar__size-buttons--button_two">
        <div class="tool-bar__size-buttons--button_inner tool-bar__size-buttons--button_two_inner"></div>
      </div>
      <div class="tool-bar__size-buttons--button_outer" id="tool-bar__size-buttons--button_three">
        <div class="tool-bar__size-buttons--button_inner tool-bar__size-buttons--button_three_inner"></div>
      </div>
      <div class="tool-bar__size-buttons--button_outer" id="tool-bar__size-buttons--button_four">
        <div class="tool-bar__size-buttons--button_inner tool-bar__size-buttons--button_four_inner"></div>
      </div>
    </div>
    <div class="tool-bar tool-bar-tools" id="${this.toolBarId}"></div>
    </div>`;
    this.buttons = [];
    this.buttonList = [
      {
        id: BUTTON_ENUM.PEN,
        className: 'icon-pen-solid',
      },
      {
        id: BUTTON_ENUM.MIRROR_PEN,
        className: 'icon-edit',
      },
      {
        id: BUTTON_ENUM.PAINT_BUCKET,
        className: 'icon-fill-solid',
      },
      {
        id: BUTTON_ENUM.PAINT_ALL_BUCKET,
        className: 'icon-fill-drip-solid',
      },
      {
        id: BUTTON_ENUM.ERASER,
        className: 'icon-eraser-solid',
      },
      {
        id: BUTTON_ENUM.STROKE,
        className: 'icon-grip-lines-solid',
      },
      {
        id: BUTTON_ENUM.RECTANGLE,
        className: 'icon-check-empty',
      },
      {
        id: BUTTON_ENUM.CIRCLE,
        className: 'icon-circle-empty',
      },
      {
        id: BUTTON_ENUM.MOVE,
        className: 'icon-hand-paper-o',
      },
      {
        id: BUTTON_ENUM.SHAPE_SELECTION,
        className: 'icon-magic-solid',
      },
      {
        id: BUTTON_ENUM.ROTATE,
        className: 'icon-left-big',
      },
      {
        id: BUTTON_ENUM.FLIP,
        className: 'icon-exchange',
      },
      {
        id: BUTTON_ENUM.LIGHTEN,
        className: 'icon-adjust-solid',
      },
      {
        id: BUTTON_ENUM.DITHERING,
        className: 'icon-chess-board-solid',
      },
      {
        id: BUTTON_ENUM.COLOR_PICKER,
        className: 'icon-vector-1',
      },
    ];
  }

  render(container) {
    $(`#${container}`).append(this.template);
    for (let i = 0; i < this.buttonList.length; i += 1) {
      const buttonSpec = this.buttonList[i];
      const button = new ToolButton(this.toolBarId, buttonSpec.className, buttonSpec.id);
      this.buttons.push(button);
      button.render();
    }
  }
}
