import $ from 'jquery';
import LayerEvent, { LAYER_EVENT_ENUM } from '../models/AppLayerEvent';
import AppLayerComponentView from '../views/appLayerComponent/AppLayerComponentView';
import ISubject from '../services/ISubject';
import ToolLayerButton from '../views/appTools/toolLayerButton/ToolLayerButton';
import AppToolTipService from '../services/AppToolTipService';
import { KEYBOARD_SHORTCUTS_PROPERTIES_ENUM } from '../models/AppKeyboardShortcutModel';
import { MODEL_EVENT_ENUM } from '../models/AppModelEvent';

export default class AppLayerComponentController extends ISubject {
  constructor(keyboardService) {
    super();
    this.element = new AppLayerComponentView();
    this.numberOfLayers = 0;
    this.initialNumberOfLayers = 1;
    this.event = null;
    this.keyboardService = keyboardService;
    this.buttons = [];
    this.currentLayer = null;
    this.windowLocked = false;
  }

  init(container) {
    this.element.render(container);
    AppToolTipService.addToolTipById(`${this.element.addButtonId}`, 'button', 'Add layer');
    AppToolTipService.addToolTipById(`${this.element.removeButtonId}`, 'button', 'Delete layer');
    AppToolTipService.addToolTipById(`${this.element.moveUpButtonId}`, 'button', 'Move layer up');
    AppToolTipService.addToolTipById(`${this.element.moveDownButtonId}`, 'button', 'Move layer down');
    for (let i = 0; i < this.initialNumberOfLayers; i += 1) {
      this.renderLayerButton();
    }
  }

  setInitialState(initialState) {
    const { numberOfLayers } = initialState;
    this.initialNumberOfLayers = numberOfLayers;
  }

  removeActive() {
    this.buttons.forEach(button => $(`#${button.buttonId}`).removeClass('button-active'));
  }

  renderLayerButton() {
    this.currentLayer = new ToolLayerButton(this.numberOfLayers + 1);
    this.buttons.push(this.currentLayer);
    this.currentLayer.render(this.element.layerListId);
    this.numberOfLayers += 1;
    this.removeActive();
    $(`#${this.currentLayer.buttonId}`).addClass('button-active');
  }

  updateLayerButtons(eventData) {
    const {
      currentLayerIndex,
      currentFrame,
    } = eventData;
    const numberOfLayers = currentFrame.length;
    if (this.buttons.length < numberOfLayers) {
      for (let i = this.buttons.length; i < currentFrame.length; i += 1) {
        this.renderLayerButton();
      }
    } else if (this.buttons.length > currentFrame.length) {
      let index = this.buttons.length - 1;
      while (index >= numberOfLayers) {
        $(`#${this.buttons[index].buttonId}`).remove();
        this.buttons.pop();
        index -= 1;
      }
    }
    this.currentLayer = this.buttons[currentLayerIndex];
    this.removeActive();
    $(`#${this.currentLayer.buttonId}`).addClass('button-active');
  }

  notifyChange(event) {
    if (event.type === MODEL_EVENT_ENUM.SET_INITIAL_STATE) {
      this.updateLayerButtons(event.data);
    }
  }

  handleAddButtonClick() {
    const event = new LayerEvent(LAYER_EVENT_ENUM.ADD_LAYER);
    this.notify(event);
    this.renderLayerButton();
  }

  handleRemoveButtonClick() {
    if (this.buttons.length > 1) {
      const index = this.buttons.indexOf(this.currentLayer);
      $(`#${this.currentLayer.buttonId}`).remove();
      if (index === 0) {
        this.currentLayer = this.buttons[index + 1];
      } else {
        this.currentLayer = this.buttons[index - 1];
      }
      this.buttons.splice(index, 1);
      this.removeActive();
      $(`#${this.currentLayer.buttonId}`).addClass('button-active');
      const event = new LayerEvent(LAYER_EVENT_ENUM.REMOVE_LAYER, index);
      this.notify(event);
      this.numberOfLayers -= 1;
    }
  }

  handleMoveDownButtonClick() {
    if (this.buttons.length > 1) {
      const index = this.buttons.indexOf(this.currentLayer);
      if (index > 0) {
        $(`#${this.currentLayer.buttonId}`).insertAfter(`#${this.buttons[index - 1].buttonId}`);
        const temp = this.buttons[index];
        this.buttons[index] = this.buttons[index - 1];
        this.buttons[index - 1] = temp;
        const event = new LayerEvent(LAYER_EVENT_ENUM.MOVE_LAYER_DOWN, index);
        this.notify(event);
      }
    }
  }

  handleMoveUpButtonClick() {
    if (this.buttons.length > 1) {
      const index = this.buttons.indexOf(this.currentLayer);
      if (index < this.buttons.length - 1) {
        $(`#${this.currentLayer.buttonId}`).insertBefore(`#${this.buttons[index + 1].buttonId}`);
        const temp = this.buttons[index];
        this.buttons[index] = this.buttons[index + 1];
        this.buttons[index + 1] = temp;
        const event = new LayerEvent(LAYER_EVENT_ENUM.MOVE_LAYER_UP, index);
        this.notify(event);
      }
    }
  }

  handleLayerButtonClick(event) {
    const layerButtonNumber = +event.target.innerText.slice(-1);
    this.currentLayer = this.buttons[layerButtonNumber - 1];
    this.removeActive();
    $(`#${this.currentLayer.buttonId}`).addClass('button-active');
    const layerEvent = new LayerEvent(LAYER_EVENT_ENUM.CHANGE_CURRENT_LAYER, layerButtonNumber - 1);
    this.notify(layerEvent);
  }

  handleKeyPress(event) {
    const {
      key, ctrlKey, shiftKey, altKey,
    } = event;
    const eventValue = JSON.stringify({
      key: key.toUpperCase(), altKey, shiftKey, ctrlKey,
    });
    const property = this.keyboardService.checkShortcutValue(eventValue);
    if (property.length > 0) {
      event.preventDefault();
      const values = Object.values(KEYBOARD_SHORTCUTS_PROPERTIES_ENUM);
      if (values.indexOf(property[0][0]) > -1) {
        switch (property[0][0]) {
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.ADD_LAYER:
            this.handleAddButtonClick();
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.DELETE_LAYER:
            this.handleRemoveButtonClick();
            break;
          default:
            break;
        }
      }
    }
    return false;
  }

  listen() {
    $(`#${this.element.addButtonId}`).on({
      click: () => {
        this.handleAddButtonClick();
      },
    });
    $(`#${this.element.removeButtonId}`).on({
      click: () => {
        this.handleRemoveButtonClick();
      },
    });
    $(`#${this.element.moveUpButtonId}`).on({
      click: () => {
        this.handleMoveUpButtonClick();
      },
    });
    $(`#${this.element.moveDownButtonId}`).on({
      click: () => {
        this.handleMoveDownButtonClick();
      },
    });
    $(`#${this.element.layerListId}`).on({
      click: event => this.handleLayerButtonClick(event),
    });
    $('body').on({
      keydown: (e) => {
        if (!this.windowLocked) {
          e.preventDefault();
          this.handleKeyPress(e);
        }
      },
    });
  }
}
