import $ from 'jquery';
import AppPreview from '../views/appPreview/AppPreviewComponent';
import ISubject from '../services/ISubject';
import { MODEL_EVENT_ENUM } from '../models/AppModelEvent';
import AppToolTipService from '../services/AppToolTipService';
import { KEYBOARD_SHORTCUTS_PROPERTIES_ENUM } from '../models/AppKeyboardShortcutModel';
import { LAYER_EVENT_ENUM } from '../models/AppLayerEvent';
import Event, { EVENT_ENUM } from '../models/AppEvent';

const INITIAL_RATE = 12;

export default class PreviewController extends ISubject {
  constructor(keyboardService) {
    super();
    this.element = new AppPreview();
    this.frames = null;
    this.rate = INITIAL_RATE;
    this.windowLocked = false;
    this.keyboardService = keyboardService;
  }

  init(container) {
    this.element.render(container);
    $('#slider').slider({
      min: 1,
      max: 24,
      value: this.rate,
    });
    this.showHint();
    AppToolTipService.addToolTipById(this.element.fullscreenButtonId, 'button', 'Fullscreen animation preview');
  }

  setInitialState(initialState) {
    const { rate } = initialState;
    this.rate = rate;
  }

  notifyChange(event) {
    if (event.type === MODEL_EVENT_ENUM.CURRENT_IMAGE_CHANGE
      || event.type === MODEL_EVENT_ENUM.FRAME_DELETE
      || event.type === MODEL_EVENT_ENUM.IMAGE_ADD
      || event.type === MODEL_EVENT_ENUM.IMAGE_LIST_UPDATE) {
      this.frames = event.data;
      this.changeAnimation();
    }
    if (event.type === LAYER_EVENT_ENUM.REMOVE_LAYER
      || event.type === LAYER_EVENT_ENUM.MOVE_LAYER_UP
      || event.type === LAYER_EVENT_ENUM.MOVE_LAYER_DOWN
      || event.type === MODEL_EVENT_ENUM.SET_INITIAL_STATE) {
      this.frames = event.data.images;
      this.changeAnimation();
    }
  }

  showHint() {
    $(`#${this.element.labelId}`).html(`FPS <span>${this.rate}</span>`);
  }

  changeAnimation() {
    this.element.startAnimation(this.frames, this.rate);
  }

  handleFullscreenButtonClick() {
    const element = document.getElementById(this.element.canvasId);
    if (!document.fullscreenElement) {
      element.requestFullscreen().catch(() => {
        $('#error-message').dialog('open');
      });
    } else {
      document.exitFullscreen();
    }
  }

  handleBodyKeydown(e) {
    const {
      key, ctrlKey, shiftKey, altKey,
    } = e;
    const eventValue = JSON.stringify({
      key: key.toUpperCase(), altKey, shiftKey, ctrlKey,
    });
    const property = this.keyboardService.checkShortcutValue(eventValue);
    if (property.length > 0) {
      e.preventDefault();
      const values = Object.values(KEYBOARD_SHORTCUTS_PROPERTIES_ENUM);
      if (values.indexOf(property[0][0]) > -1) {
        switch (property[0][0]) {
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.FULLSCREEN:
            this.handleFullscreenButtonClick();
            break;
          default:
            break;
        }
      }
    }
    return false;
  }

  listen() {
    $('#slider').slider({
      create: () => {
        $(`#${this.element.sliderId}`).text($(this).slider('value'));
      },
      slide: (event, ui) => {
        $(`#${this.element.sliderId}`).text(ui.value);
      },
      change: (event, ui) => {
        const targetValue = ui.value;
        this.rate = +targetValue;
        const rateEvent = new Event(EVENT_ENUM.RATE_CHANGE, { rate: this.rate });
        this.notify(rateEvent);
        this.showHint();
        this.changeAnimation();
      },
    });
    $(`#${this.element.fullscreenButtonId}`).on({
      click: () => {
        this.handleFullscreenButtonClick();
      },
    });
    $('body').on({
      keydown: (event) => {
        if (!this.windowLocked) {
          this.handleBodyKeydown(event);
        }
      },
    });
  }
}
