import $ from 'jquery';
import './appLayerComponentStyles.less';

export default class AppLayerComponentView {
  constructor() {
    this.elementId = 'layer-component';
    this.buttonBarId = 'layer-component__button-bar';
    this.addButtonId = 'layer-component__button-bar--add';
    this.removeButtonId = 'layer-component__button-bar--remove';
    this.moveUpButtonId = 'layer-component__button-bar--move_up';
    this.moveDownButtonId = 'layer-component__button-bar--move_down';
    this.layerListId = 'layer-component__layer-list';
    this.template = `<div class="${this.elementId}" id="${this.elementId}">
      <h3 class="layer-component__heading">Layers</h3>
      <div class="layer-component__container">
        <div class="${this.buttonBarId}" id="${this.buttonBarId}">
          <button class="layer-component__button-bar--button" id="${this.addButtonId}">
            <i class="layer-component__button-bar--button_icon icon-plus"></i>
          </button>
          <button class="layer-component__button-bar--button" id="${this.removeButtonId}">
            <i class="layer-component__button-bar--button_icon icon-cancel"></i>
          </button>
          <button class="layer-component__button-bar--button" id="${this.moveUpButtonId}">
            <i class="layer-component__button-bar--button_icon icon-up-big"></i>
          </button>
          <button class="layer-component__button-bar--button" id="${this.moveDownButtonId}">
            <i class="layer-component__button-bar--button_icon icon-down-big"></i>
          </button>
        </div>
        <div class="${this.layerListId}" id="${this.layerListId}"></div>
      </div>
    </div>`;
  }

  render(container) {
    $(`#${container}`).append(this.template);
    $(`#${this.elementId}`).accordion({
      active: 0,
      animate: 200,
      collapsible: true,
      header: 'h3',
      heightStyle: 'content',
    });
  }
}
