import $ from 'jquery';
import { BUTTON_ENUM } from '../views/appToolBarView/AppToolBarView';

export default class AppToolTipService {
  static addToolTipById(id, item, tip) {
    $(`#${id}`).tooltip({
      items: item,
      content: tip,
    });
  }

  static addToolTipByClass(className, item, tip) {
    $(`.${className}`).tooltip({
      items: item,
      content: tip,
    });
  }

  static addToolTips() {
    $(`#${BUTTON_ENUM.PEN}`).tooltip({
      items: 'i',
      content: 'Pen. Left button - primary color. Right button - secondary color.',
    });
    $(`#${BUTTON_ENUM.MIRROR_PEN}`).tooltip({
      items: 'i',
      content: 'Mirror pen.Left button - primary color. Right button - secondary color.',
    });
    $(`#${BUTTON_ENUM.PAINT_BUCKET}`).tooltip({
      items: 'i',
      content: 'Paint bucket. Left button - primary color. Right button - secondary color.',
    });
    $(`#${BUTTON_ENUM.PAINT_ALL_BUCKET}`).tooltip({
      items: 'i',
      content: 'Paint all pixels of the same color. Left button - primary color. Right button - secondary color.',
    });
    $(`#${BUTTON_ENUM.ERASER}`).tooltip({
      items: 'i',
      content: 'Eraser.',
    });
    $(`#${BUTTON_ENUM.STROKE}`).tooltip({
      items: 'i',
      content: 'Stroke.',
    });
    $(`#${BUTTON_ENUM.MOVE}`).tooltip({
      items: 'i',
      content: 'Move the image.',
    });
    $(`#${BUTTON_ENUM.LIGHTEN}`).tooltip({
      items: 'i',
      content: 'Lighten. Hold \'Shift\' to darken.',
    });
    $(`#${BUTTON_ENUM.DITHERING}`).tooltip({
      items: 'i',
      content: 'Dithering.',
    });
    $(`#${BUTTON_ENUM.COLOR_PICKER}`).tooltip({
      items: 'i',
      content: 'Click the canvas to set a color. Left button - primary color. Right button - secondary color.',
    });
    $(`#${BUTTON_ENUM.SHAPE_SELECTION}`).tooltip({
      items: 'i',
      content: 'Shape selection. Click on the canvas and drag the selected area. Press \'Enter\' to apply changes. Hold \'Shift\' to move the content permanently.',
    });
    $(`#${BUTTON_ENUM.CIRCLE}`).tooltip({
      items: 'i',
      content: 'Ellipse. Hold \'Shift\' to keep 1 to 1 ratio.',
    });
    $(`#${BUTTON_ENUM.RECTANGLE}`).tooltip({
      items: 'i',
      content: 'Rectangle. Hold \'Shift\' to keep 1 to 1 ratio.',
    });
    $(`#${BUTTON_ENUM.FLIP}`).tooltip({
      items: 'i',
      content: 'Click on the canvas to flip the image horizontally. Hold \'Shift\' for vertical flip. Hold \'Ctrl\' for diagonal flip.',
    });
    $(`#${BUTTON_ENUM.ROTATE}`).tooltip({
      items: 'i',
      content: 'Click on the canvas to rotate the image to the left. Hold \'Shift\' to rotate to the right.',
    });
  }
}
