import $ from 'jquery';
import './canvasStyles.less';
import AppColorConverter from '../../services/AppColorConverter';

export default class AppCanvasView {
  constructor(size, width, color) {
    this.elementId = 'canvas-main';
    this.canvasContainerId = 'canvas-main__container';
    this.size = size;
    this.width = width;
    this.baseColor = color;
    this.squareWidth = this.width / this.size;
    this.template = `<div class="canvas-main__container" id="${this.canvasContainerId}" width="${this.width + 1}px" height="${this.width + 1}px">
      <canvas class="canvas-main" id="${this.elementId}" width="${this.width}px" height="${this.width}px"
      style="background-color: ${this.baseColor};">
    </div>`;
    this.image = null;
    this.container = null;
    this.currentState = null;
    this.colorConverter = new AppColorConverter();
  }

  render(container) {
    this.container = container;
    $(`#${container}`).append(this.template);
  }

  createImage() {
    const canvas = document.getElementById(this.elementId);
    this.image = canvas.toDataURL();
    return this.image;
  }

  updateCanvas(coordinates, color, mode) {
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    ctx.globalCompositeOperation = mode;
    ctx.fillStyle = color;
    if (coordinates) {
      for (let i = 0; i < coordinates.length; i += 1) {
        const {
          indexX: x,
          indexY: y,
        } = coordinates[i];
        if (x >= 0 && x < this.size && y >= 0 && y < this.size) {
          ctx.fillRect(x * this.squareWidth,
            y * this.squareWidth,
            this.squareWidth, this.squareWidth);
        }
      }
    }
  }

  paint(coordinates, color) {
    this.updateCanvas(coordinates, color, 'source-over');
  }

  erase(coordinates) {
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    if (coordinates) {
      for (let i = 0; i < coordinates.length; i += 1) {
        const {
          indexX: x,
          indexY: y,
        } = coordinates[i];
        if (x >= 0 && x < this.size && y >= 0 && y < this.size) {
          ctx.clearRect(x * this.squareWidth,
            y * this.squareWidth,
            this.squareWidth, this.squareWidth);
        }
      }
    }
  }

  lighten(coordinates, lightness, frame) {
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    if (coordinates) {
      coordinates.forEach((val) => {
        const {
          indexX: x,
          indexY: y,
        } = val;
        if (x >= 0 && x < this.size && y >= 0 && y < this.size) {
          let color = frame[x][y];
          if (color !== this.baseColor) {
            color = AppColorConverter.changeColor(color, lightness);
            ctx.fillStyle = color;
            ctx.fillRect(x * this.squareWidth,
              y * this.squareWidth,
              this.squareWidth, this.squareWidth);
          }
        }
      });
    }
  }

  dither(coordinates, primaryColor, secondaryColor) {
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    if (coordinates) {
      coordinates.forEach((val) => {
        const {
          indexX: x,
          indexY: y,
        } = val;
        if (x >= 0 && x < this.size && y >= 0 && y < this.size) {
          if (x % 2 === y % 2) {
            ctx.fillStyle = primaryColor;
          } else {
            ctx.fillStyle = secondaryColor;
          }
          ctx.fillRect(x * this.squareWidth,
            y * this.squareWidth,
            this.squareWidth, this.squareWidth);
        }
      });
    }
  }

  clear() {
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    ctx.clearRect(0, 0, this.width, this.width);
  }

  drawNewFrame(frame) {
    this.clear();
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    frame.forEach((row, i) => {
      row.forEach((column, j) => {
        ctx.fillStyle = column;
        ctx.fillRect(i * this.squareWidth, j * this.squareWidth,
          this.squareWidth, this.squareWidth);
      });
    });
  }

  saveCurrentState() {
    const canvas = document.getElementById(this.elementId);
    const ctx = canvas.getContext('2d');
    this.currentState = ctx.getImageData(0, 0, this.width, this.width);
  }

  restoreInitialState() {
    if (this.currentState) {
      const canvas = document.getElementById(this.elementId);
      const ctx = canvas.getContext('2d');
      ctx.putImageData(this.currentState, 0, 0);
    }
  }
}
