import $ from 'jquery';
import './previewComponentstyles.less';

export default class AppPreviewComponent {
  constructor() {
    this.id = 'preview-component';
    this.sliderId = 'preview-slider';
    this.canvasId = 'preview-canvas';
    this.imageClass = 'preview-image';
    this.labelId = 'preview-label';
    this.fullscreenButtonId = 'preview-component__button-fullscreen';
    this.currentInterval = null;
    this.currentRate = 0;
    this.template = `<div class="preview-component" id="${this.id}">
      <div class="preview-canvas" id="${this.canvasId}">
        <div class="${this.imageClass}"></div>
      </div>
      <div class="preview-component__tools">
        <p class="preview-label" id="${this.labelId}"></p>
        <button class="${this.fullscreenButtonId}" id="${this.fullscreenButtonId}">
          <i class="preview-component__button-fullscreen--icon icon-television"></i>
        </button>
      </div>
      <div class="slider" id="slider">
        <div class="ui-slider-handle" id="${this.sliderId}"></div>
      </div>
      <p class="error-message" id="error-message">Error attempting to enable full-screen mode</p>
    </div>`;
  }

  render(container) {
    $(`#${container}`).append(this.template);
    $('#error-message').dialog({
      autoOpen: false,
      dialogClass: 'no-close',
      width: 500,
      height: 250,
    });
    $('#error-message').dialog({
      buttons: [
        {
          text: 'Ok',
          click: () => {
            $('#error-message').dialog('close');
          },
        },
      ],
    });
  }

  draw(frame) {
    let url = '';
    if (frame.length === 1) {
      url = `url('${frame[0]}') `;
    } else {
      for (let i = frame.length - 1; i >= 0; i -= 1) {
        url += `url('${frame[i]}'),`;
      }
    }

    $(`.${this.imageClass}`).css('background-image', url.slice(0, -1));
  }

  startAnimation(frames, rate) {
    const currRate = rate;
    if (frames && frames.length > 0) {
      let count = 1;
      this.currentRate = Math.round(1000 / currRate);
      if (this.currentInterval) {
        clearInterval(this.currentInterval);
      }
      this.currentInterval = setInterval(() => {
        const frame = frames[count % frames.length];
        this.draw(frame);
        count += 1;
      }, this.currentRate);
    }
  }
}
