

export default class AppFrameModifyService {
  static checkCoordinates(initialX, initialY, finalX, finalY, frame) {
    if (initialX >= 0 && initialX < frame[0].length
      && initialY >= 0 && initialY < frame[0].length
      && finalX >= 0 && finalX < frame[0].length
      && finalY >= 0 && finalY < frame[0].length) {
      return true;
    }
    return false;
  }

  static getMaxAndMinCoords(initialX, initialY, finalX, finalY) {
    return {
      startX: Math.min(initialX, finalX),
      endX: Math.max(initialX, finalX),
      startY: Math.min(initialY, finalY),
      endY: Math.max(initialY, finalY),
    };
  }

  static rotateFrameToLeft(initialX, initialY, finalX, finalY, frame) {
    const modifiedFramePart = [];
    if (AppFrameModifyService.checkCoordinates(initialX, initialY, finalX, finalY, frame)) {
      const {
        startX, endX, startY, endY,
      } = AppFrameModifyService.getMaxAndMinCoords(initialX, initialY, finalX, finalY);
      let maxX = endX;
      let minY = startY;
      let count = 0;
      while (minY !== endY + 1 && minY < frame[0].length) {
        modifiedFramePart.push([]);
        while (maxX !== startX - 1 && maxX >= 0) {
          modifiedFramePart[count].push(frame[maxX][minY]);
          maxX -= 1;
        }
        maxX = endX;
        minY += 1;
        count += 1;
      }
    }
    return modifiedFramePart;
  }

  static rotateFrameToRight(initialX, initialY, finalX, finalY, frame) {
    const modifiedFramePart = [];
    if (AppFrameModifyService.checkCoordinates(initialX, initialY, finalX, finalY, frame)) {
      const {
        startX, endX, startY, endY,
      } = AppFrameModifyService.getMaxAndMinCoords(initialX, initialY, finalX, finalY);
      let minX = startX;
      let maxY = endY;
      let count = 0;
      while (startY - 1 !== maxY && maxY >= 0) {
        modifiedFramePart.push([]);
        while (endX + 1 !== minX && minX < frame[0].length) {
          modifiedFramePart[count].push(frame[minX][maxY]);
          minX += 1;
        }
        minX = startX;
        maxY -= 1;
        count += 1;
      }
    }
    return modifiedFramePart;
  }

  static flipFrameDiagonally(initialX, initialY, finalX, finalY, frame) {
    const modifiedFramePart = [];
    if (AppFrameModifyService.checkCoordinates(initialX, initialY, finalX, finalY, frame)) {
      const {
        startX, endX, startY, endY,
      } = AppFrameModifyService.getMaxAndMinCoords(initialX, initialY, finalX, finalY);
      let maxX = endX;
      let maxY = endY;
      let count = 0;
      while (startY - 1 !== maxY && maxY >= 0) {
        modifiedFramePart.push([]);
        while (startX - 1 !== maxX && maxX >= 0) {
          modifiedFramePart[count].push(frame[maxX][maxY]);
          maxX -= 1;
        }
        maxX = endX;
        maxY -= 1;
        count += 1;
      }
    }
    return modifiedFramePart;
  }

  static flipFrameHorizontally(initialX, initialY, finalX, finalY, frame) {
    const modifiedFramePart = [];
    if (AppFrameModifyService.checkCoordinates(initialX, initialY, finalX, finalY, frame)) {
      const {
        startX, endX, startY, endY,
      } = AppFrameModifyService.getMaxAndMinCoords(initialX, initialY, finalX, finalY);
      let maxX = endX;
      let minY = startY;
      let count = 0;
      while (startX - 1 !== maxX && maxX >= 0) {
        modifiedFramePart.push([]);
        while (endY + 1 !== minY && minY < frame[0].length) {
          modifiedFramePart[count].push(frame[maxX][minY]);
          minY += 1;
        }
        minY = startY;
        maxX -= 1;
        count += 1;
      }
    }
    return modifiedFramePart;
  }

  static flipFrameVertically(initialX, initialY, finalX, finalY, frame) {
    const modifiedFramePart = [];
    if (AppFrameModifyService.checkCoordinates(initialX, initialY, finalX, finalY, frame)) {
      const {
        startX, endX, startY, endY,
      } = AppFrameModifyService.getMaxAndMinCoords(initialX, initialY, finalX, finalY);
      let minX = startX;
      let maxY = endY;
      let count = 0;
      while (endX + 1 !== minX && minX < frame[0].length) {
        modifiedFramePart.push([]);
        while (startY - 1 !== maxY && maxY >= 0) {
          modifiedFramePart[count].push(frame[minX][maxY]);
          maxY -= 1;
        }
        maxY = endY;
        minX += 1;
        count += 1;
      }
    }
    return modifiedFramePart;
  }

  static increaseFrameSize(layer, prevValue, value, divisor) {
    const changedArray = new Array(value);
    for (let i = 0; i < value; i += 1) {
      changedArray[i] = new Array(value);
    }
    for (let i = 0; i < prevValue; i += 1) {
      for (let j = 0; j < prevValue; j += 1) {
        for (let k = 0; k < divisor; k += 1) {
          for (let l = 0; l < divisor; l += 1) {
            changedArray[i * divisor + k][j * divisor + l] = layer[i][j];
          }
        }
      }
    }
    return changedArray;
  }

  static decreaseFrameSize(layer, prevValue, value, divisor) {
    const changedArray = new Array(value);
    for (let i = 0; i < value; i += 1) {
      changedArray[i] = new Array(value);
    }
    for (let i = 0; i < value; i += 1) {
      for (let j = 0; j < value; j += 1) {
        changedArray[i][j] = layer[i * divisor][j * divisor];
      }
    }
    return changedArray;
  }

  static decreaseCoordinates(coords, divisor) {
    const changedCoords = [];
    for (let i = 0; i < coords.length; i += 1) {
      const currentCoords = coords[i];
      changedCoords.push([Math.floor(currentCoords[0] / divisor),
        Math.floor(currentCoords[1] / divisor)]);
    }
    return changedCoords;
  }

  static increaseCoordinates(coords, divisor) {
    const changedCoords = [];
    for (let i = 0; i < coords.length; i += 1) {
      const currentCoords = coords[i];
      for (let k = 0; k < divisor; k += 1) {
        for (let l = 0; l < divisor; l += 1) {
          changedCoords.push([currentCoords[0] * divisor + k, currentCoords[1] * divisor + l]);
        }
      }
    }
    return changedCoords;
  }
}
