import $ from 'jquery';
import './toolFrameStyles.less';

export default class ToolFrame {
  constructor(width, number) {
    this.number = number;
    this.width = width;
    this.template = `<div class="frame-component__frame" id="${this.number}">
      <p class="frame-number">${this.number}</p>
      <button class="button-layer delete">
        <i class="icon-trash" data-attribute=""></i>
      </button>
      <button class="button-layer duplicate">
        <i class="icon-docs" data-attribute=""></i>
      </button>
      <button class="button-layer move-layer">
        <i class="icon-move" data-attribute=""></i>
      </button>
      <canvas class = "frame-canvas" id="canvas-${this.number}"width="${width}px" height ="${width}px"></canvas>
    </div>`;
  }

  render(element) {
    $(element).append(this.template);
  }

  refreshId(number) {
    const prevNumber = this.number;
    this.number = number + 1;
    const elem = $(`#${prevNumber}`);
    $(elem).children('.frame-number').html(`${this.number}`);
    $(elem).children('.frame-canvas').attr('id', `canvas-${this.number}`);
    $(elem).attr('id', `${this.number}`);
  }

  clear() {
    const canvas = document.getElementById(`canvas-${this.number}`);
    const ctx = canvas.getContext('2d');
    ctx.clearRect(0, 0, this.width, this.width);
  }

  drawFrame(frame, numberOfSquares) {
    const squareWidth = this.width / numberOfSquares;
    const canvas = document.getElementById(`canvas-${this.number}`);
    if (canvas) {
      const ctx = canvas.getContext('2d');
      frame.forEach((row, i) => {
        row.forEach((column, j) => {
          ctx.fillStyle = column;
          ctx.fillRect(i * squareWidth, j * squareWidth, squareWidth, squareWidth);
        });
      });
    }
  }
}
