import $ from 'jquery';
import './appSettingsBarStyles.less';

export default class AppSettingsBarView {
  constructor() {
    this.elementId = 'settings-bar';
    this.infoWindowId = 'information-window';
    this.minButtonId = 'settings-bar__button--min';
    this.medButtonId = 'settings-bar__button--med';
    this.largeButtonId = 'settings-bar__button--large';
    this.frameInfoId = 'information-window__frame-info';
    this.coordsInfoId = 'information-window__coords-info';
    this.canvasInfoId = 'information-window__canvas-info';
    this.exportAsPawskelId = 'settings-bar__file-save--pawskel_export';
    this.exportAsApngId = 'settings-bar__file-save--apng_export';
    this.exportAsGifId = 'settings-bar__file-save--gif_export';
    this.importPawskelId = 'settings-bar__file-save--pawskel_import';
    this.exportSettingsDialogId = 'export-settings__dialog';
    this.exportsettingsNameId = 'export-settings__dialog--name';
    this.exportsettingsSizeId = 'export-settings__dialog--size';
    this.exportSettingsDialogSubmitButtonId = 'export-settings__dialog--submit';
    this.template = `<div class="${this.elementId}" id="${this.elementId}">
    <ul class="settings-bar__header">
      <li><a href="#settings-bar__size-settings">
        <i class="settings-bar__icon icon-vector-square-solid"></i>
      </a></li>
      <li><a href="#settings-bar__file-save">
        <i class="settings-bar__icon icon-save-solid"></i>
      </a></li>
    </ul>
    <form class="settings-bar__size-settings--form" id="settings-bar__size-settings">
      <fieldset class="settings-bar__size-settings--fieldset">
        <legend>Select canvas size</legend>
          <div id="radio">
            <input class="settings-bar__size-button ${this.minButtonId}" id="${this.minButtonId}" type="radio" name="size-button" checked>
            <label class="settings-bar__size-button--label" for="${this.minButtonId}">32x32</label>
            <input  class="settings-bar__size-button ${this.medButtonId}" id="${this.medButtonId}" type="radio" name="size-button">
            <label class="settings-bar__size-button--label" for="${this.medButtonId}">64x64</label>
            <input class="settings-bar__size-button ${this.largeButtonId}" id="${this.largeButtonId}" type="radio" name="size-button">
            <label class="settings-bar__size-button--label" for="${this.largeButtonId}">128x128</label>
          </div>
        </fieldset>
      </form>
      <div class="settings-bar__file-save--form" id="settings-bar__file-save">
    <button class="settings-bar__button settings-bar__file-save__button" id="${this.exportAsPawskelId}">Export as Pawskel</button>
    <button class="settings-bar__button settings-bar__file-save__button" id="${this.exportAsApngId}">Export as APNG</button>
    <button class="settings-bar__button settings-bar__file-save__button" id="${this.exportAsGifId}">Export as Gif</button>
    <input class="settings-bar__button settings-bar__file-save__input" id="${this.importPawskelId}" type="file" accept=".pawskel" width="150px">
  </div>
  <div class="${this.exportSettingsDialogId}" id="${this.exportSettingsDialogId}">
    <label class="export-settings__dialog--label" for="${this.exportsettingsNameId}">Enter file name</label>
    <input class="export-settings__dialog--input" id="${this.exportsettingsNameId}" type="text" placeholder="NewPawskel" onfocus="this.value=''" autofocus>
    <label class="export-settings__dialog--label" for="${this.exportsettingsSizeId}">Enter canvas width</label>
    <input class="export-settings__dialog--input" id="${this.exportsettingsSizeId}" type="text" placeholder="800">
    <button class="save-dialod__button" id="${this.exportSettingsDialogSubmitButtonId}">Save</button>
  </div>
  </div>
  <div class="${this.infoWindowId}" id="${this.infoWindowId}">
  <div class="information-window__canvas-info">
    <p class="information-window__info-field" id="${this.canvasInfoId}"></p>
    <p class="information-window__info-field" id="${this.coordsInfoId}"></p>
  </div>
    <p class="information-window__info-field" id="${this.frameInfoId}"></p>
  </div>
  <p class="error-message" id="open-error-message">No files have been selected</p>
  <div class="warning-message" id="warning-message">
    <p>You will not be able to restore current state after opening new file. If you do not want to lose your work, save it to your desktop.</p>
  </div>
   `;
  }

  render(container) {
    $(`#${container}`).append(this.template);
    $(`#${this.elementId}`).tabs({
      collapsible: true,
      hide: { effect: 'explode', duration: 1000 },
      show: { effect: 'blind', duration: 800 },
    });
    $('#radio').buttonset({
      items: 'input[type=radio]',
    });
    $(`#${this.exportSettingsDialogId}`).dialog({
      autoOpen: false,
      dialogClass: 'no-close',
      width: 500,
      height: 250,
    });
    $(`#${this.saveDialogId}`).dialog({
      autoOpen: false,
      dialogClass: 'no-close',
      width: 500,
      height: 250,
    });
    $(`#${this.exportSettingsDialogId}`).dialog({
      buttons: [
        {
          text: 'Cancel',
          click: () => {
            $(`#${this.exportSettingsDialogId}`).dialog('close');
          },
        },
      ],
    });
    $(`#${this.exportAsPawskelId}`).on({
      click: () => {
        $(`#${this.exportSettingsDialogId}`).dialog('open');
      },
    });
    $(`#${this.exportAsApngId}`).on({
      click: () => {
        $(`#${this.exportSettingsDialogId}`).dialog('open');
      },
    });
    $(`#${this.exportAsGifId}`).on({
      click: () => {
        $(`#${this.exportSettingsDialogId}`).dialog('open');
      },
    });
    $('#open-error-message').dialog({
      autoOpen: false,
      dialogClass: 'no-close',
      width: 500,
      height: 250,
    });
    $('#open-error-message').dialog({
      buttons: [
        {
          text: 'Ok',
          click: () => {
            $('#open-error-message').dialog('close');
          },
        },
      ],
    });
    $('#warning-message').dialog({
      autoOpen: false,
      dialogClass: 'no-close',
      width: 500,
      height: 250,
    });
    $('#warning-message').dialog({
      buttons: [
        {
          text: 'Ok',
          click: () => {
            $('#warning-message').dialog('close');
          },
        },
      ],
    });
  }
}
