import $ from 'jquery';
import AppLandingPageView from '../views/appLandingPageView/AppLandingPageView';

export default class AppLandingPageController {
  constructor(controller) {
    this.controller = controller;
    this.element = new AppLandingPageView();
  }

  init() {
    this.element.render('body');
    this.listen();
  }

  hide() {
    $(`#${this.element.elementId}`).hide();
  }

  listen() {
    $(`#${this.element.newButtonId}`).on({
      click: () => {
        localStorage.clear();
        this.controller.start();
      },
    });
    $(`#${this.element.continueButtonId}`).on({
      click: () => {
        this.controller.start();
      },
    });
  }
}
