export default class Frame {
  constructor(id, numberOfSquares, basicColor) {
    this.id = id;
    this.numberOfSquares = numberOfSquares;
    this.basicColor = basicColor;
    this.currentLayer = null;
    this.images = [];
    this.layers = [];
  }

  addLayer() {
    this.currentLayer = new Array(this.numberOfSquares);
    for (let i = 0; i < this.numberOfSquares; i += 1) {
      this.currentLayer[i] = new Array(this.numberOfSquares);
      for (let j = 0; j < this.numberOfSquares; j += 1) {
        this.currentLayer[i][j] = this.basicColor;
      }
    }
    this.layers.push(this.currentLayer);
    return this.currentLayer;
  }

  addImage(image) {
    this.images.push(image);
  }
}
