import $ from 'jquery';
import firebase from 'firebase/app';
import CanvasController from './AppCanvasController';
import ToolBarController from './AppToolBarController';
import BodyView from '../views/appBodyView/AppBodyView';
import FrameController from './AppFrameController';
import AppFrameService from '../services/AppFrameService';
import { EVENT_ENUM } from '../models/AppEvent';
import PreviewController from './AppPreviewController';
import { BUTTON_ENUM } from '../views/appToolBarView/AppToolBarView';
import AppColorPickerController from './AppColorPickerController';
import AppKeyboardShortcutService from '../services/AppKeyboardShortcutService';
import { KEYBOARD_SHORTCUTS_PROPERTIES_ENUM } from '../models/AppKeyboardShortcutModel';
import { LAYER_EVENT_ENUM } from '../models/AppLayerEvent';
import { SETTINGS_EVENT_ENUM } from '../models/AppSettingsEvent';
import AppModalDialogController from './AppModalDialogController';
import AppLayerComponentController from './AppLayerComponentController';
import AppSettingsBarController from './AppSettingsBarController';
import 'firebase/auth';
import AppHeaderController from './AppHeaderController';
import AppFilesaverService from '../services/AppFilesaverService';
import AppLandingPageController from './AppLandingPageController';

const NUMBER_OF_SQUARES = 32;
const CANVAS_WIDTH = {
  EXTRA_LARGE: 896,
  LARGE: 768,
  MEDIUM: 640,
  SMALL: 512,
  EXTRA_SMALL: 384,
};
const BACKGROUND_COLOR = 'rgba(0, 0, 0, 0)';
const PRIMARY_COLOR = '#000000';
const SECONDARY_COLOR = '#FF0000';
const PEN_SIZE = 1;
const LIGHTEN = 5;
const DARKEN = -5;
const DEPENDANTS_ENUM = {
  TOOL_BAR: 'toolBarController',
  CANVAS: 'canvasController',
  FRAME_CONTROLLER: 'frameController',
  PREVIEW: 'previewController',
  COLOR_PICKER: 'colorPicker',
  MODAL_DIALOG: 'modalDialog',
  LAYER_COMPONENT: 'layerComponent',
  SETTINGS_BAR: 'settingsBar',
  HEADER: 'header',
  LANDING_PAGE: 'landingPage',
};
const firebaseConfig = {
  apiKey: 'AIzaSyBB9DL55XKaP77affXxrdWouJjv7STlDH0',
  authDomain: 'piskel-clone-1562023132472.firebaseapp.com',
  databaseURL: 'https://piskel-clone-1562023132472.firebaseio.com',
  projectId: 'piskel-clone-1562023132472',
  storageBucket: '',
  messagingSenderId: '1006168864032',
  appId: '1:1006168864032:web:17b5b24c38b44ed2',
};
export default class AppController {
  constructor() {
    this.componentControllers = {};
    this.canvasWidth = CANVAS_WIDTH.LARGE;
    this.numberOfSquares = NUMBER_OF_SQUARES;
    this.container = new BodyView();
    this.currentTool = BUTTON_ENUM.PEN;
    this.currentColor = PRIMARY_COLOR;
    this.primaryColor = PRIMARY_COLOR;
    this.secondaryColor = SECONDARY_COLOR;
    this.penSize = PEN_SIZE;
    this.frameService = new AppFrameService(this.numberOfSquares, BACKGROUND_COLOR);
    this.keyboardService = new AppKeyboardShortcutService();
    this.fileSaver = new AppFilesaverService(this.frameService);
    this.landingPageController = new AppLandingPageController(this);
    this.windowLocked = false;
    this.signedIn = false;
  }

  checkWindowSize() {
    const windowHeight = window.innerHeight;
    if (windowHeight >= 1000) {
      this.canvasWidth = CANVAS_WIDTH.EXTRA_LARGE;
    }
    if (windowHeight < 1000 && windowHeight >= 900) {
      this.canvasWidth = CANVAS_WIDTH.LARGE;
    }
    if (windowHeight < 900 && windowHeight >= 720) {
      this.canvasWidth = CANVAS_WIDTH.MEDIUM;
    }
    if (windowHeight < 720 && windowHeight >= 520) {
      this.canvasWidth = CANVAS_WIDTH.SMALL;
    }
    if (windowHeight < 520) {
      this.canvasWidth = CANVAS_WIDTH.EXTRA_SMALL;
    }
  }

  saveCurrentState() {
    const currentState = {
      numberOfSquares: this.numberOfSquares,
      currentTool: this.currentTool,
      primaryColor: this.primaryColor,
      secondaryColor: this.secondaryColor,
      penSize: this.componentControllers[DEPENDANTS_ENUM.TOOL_BAR].currentPenSize,
      keyboardShortcuts: this.keyboardService.getShortCuts(),
      appFrames: this.frameService.getFrames(),
      currentFrameIndex: this.frameService.frameList.frames.indexOf(this.frameService.currentFrame),
      currentLayerIndex: this.frameService.currentFrame.layers
        .indexOf(this.frameService.currentFrame.currentLayer),
      provider: this.componentControllers[DEPENDANTS_ENUM.HEADER].provider,
      numberOfLayers: this.frameService.currentFrame.layers.length,
      currentbuttonShortcuts: this.componentControllers[DEPENDANTS_ENUM.MODAL_DIALOG]
        .getFinalShortcuts(),
      rate: this.componentControllers[DEPENDANTS_ENUM.PREVIEW].rate,
    };
    window.localStorage.setItem('currentState', JSON.stringify(currentState));
  }

  restoreCurrentState() {
    if (window.localStorage.getItem('currentState')) {
      const currentState = JSON.parse(window.localStorage.getItem('currentState'));
      const {
        numberOfSquares,
        currentTool,
        primaryColor,
        secondaryColor,
        penSize,
        keyboardShortcuts,
        appFrames,
        currentFrameIndex,
        currentLayerIndex,
        provider,
        numberOfLayers,
        currentbuttonShortcuts,
        rate,
      } = currentState;
      this.numberOfSquares = numberOfSquares;
      this.currentTool = currentTool;
      this.primaryColor = primaryColor;
      this.secondaryColor = secondaryColor;
      this.penSize = penSize;
      this.frameService.setFrame(
        appFrames, currentFrameIndex, currentLayerIndex, this.numberOfSquares,
      );
      this.keyboardService.setShortcuts(keyboardShortcuts);
      Object.values(this.componentControllers).forEach((comp) => {
        comp.setInitialState({
          numberOfSquares,
          currentTool,
          primaryColor,
          secondaryColor,
          penSize,
          keyboardShortcuts,
          provider,
          numberOfLayers,
          currentbuttonShortcuts,
          rate,
        });
      });
    }
  }

  init() {
    this.checkWindowSize();
    this.componentControllers[DEPENDANTS_ENUM.TOOL_BAR] = new ToolBarController(
      this.keyboardService,
    );
    this.componentControllers[DEPENDANTS_ENUM.CANVAS] = new CanvasController(
      this.numberOfSquares,
      this.canvasWidth, BACKGROUND_COLOR, this.primaryColor, this.secondaryColor,
    );
    this.componentControllers[DEPENDANTS_ENUM.FRAME_CONTROLLER] = new FrameController(
      BACKGROUND_COLOR, this.numberOfSquares, this.keyboardService,
    );
    this.componentControllers[DEPENDANTS_ENUM.PREVIEW] = new PreviewController(
      this.keyboardService,
    );
    this.componentControllers[DEPENDANTS_ENUM.COLOR_PICKER] = new AppColorPickerController(
      this.primaryColor, this.secondaryColor,
    );
    this.componentControllers[DEPENDANTS_ENUM.MODAL_DIALOG] = new AppModalDialogController(
      this.keyboardService,
    );
    this.componentControllers[DEPENDANTS_ENUM.LAYER_COMPONENT] = new AppLayerComponentController(
      this.keyboardService,
    );
    this.componentControllers[DEPENDANTS_ENUM.SETTINGS_BAR] = new AppSettingsBarController(
      this.keyboardService,
      this.fileSaver,
    );
    this.componentControllers[DEPENDANTS_ENUM.HEADER] = new AppHeaderController();
    firebase.initializeApp(firebaseConfig);
    this.landingPageController.init();
  }

  start() {
    this.landingPageController.hide();
    this.restoreCurrentState();
    this.container.render();
    Object.values(this.componentControllers).forEach((comp) => {
      comp.subscribe(this);
      comp.init(this.container.containerId);
      comp.listen();
      this.frameService.subscribe(comp);
    });
    this.componentControllers[DEPENDANTS_ENUM.SETTINGS_BAR].setCanvasInfo(this.numberOfSquares);
    this.listen();
    this.frameService.init(this.numberOfSquares);
    this.keyboardService.init();
  }

  loadNewFile(eventData) {
    localStorage.clear();
    const {
      name,
      numberOfSquares,
      frames,
    } = eventData;
    this.numberOfSquares = numberOfSquares;
    this.frameService.clearFrames(numberOfSquares);
    this.frameService.setFrameFromFile(frames, 0, 0, numberOfSquares);
    this.componentControllers[DEPENDANTS_ENUM.HEADER].updateFileTitle(name);
  }

  refreshFrame(e) {
    if (this.frameService.currentFrame && e.coordinates) {
      if (this.currentTool === BUTTON_ENUM.ERASER) {
        this.currentColor = BACKGROUND_COLOR;
      } else {
        this.setCurrentColor(e);
      }
      for (let i = 0; i < e.coordinates.length; i += 1) {
        const x = e.coordinates[i].indexX;
        const y = e.coordinates[i].indexY;
        this.frameService.refreshFrame(x, y, this.currentColor);
      }
    }
  }

  ditherFrame(e, primaryColor, secondaryColor) {
    if (this.frameService.currentFrame && e.coordinates) {
      e.coordinates.forEach((val) => {
        const {
          indexX: x,
          indexY: y,
        } = val;
        if (x >= 0 && x < NUMBER_OF_SQUARES && y >= 0 && y < NUMBER_OF_SQUARES) {
          if (x % 2 === y % 2) {
            this.frameService.refreshFrame(x, y, primaryColor);
          } else {
            this.frameService.refreshFrame(x, y, secondaryColor);
          }
        }
      });
    }
  }

  renderFrame() {
    this.frameService.addFrame(this.numberOfSquares);
  }

  drawFrame() {
    const {
      image: currentImage,
    } = this.componentControllers[DEPENDANTS_ENUM.CANVAS];
    if (this.currentTool === BUTTON_ENUM.ERASER) {
      this.frameService.finishErasedFrameChange(currentImage);
    } else {
      this.frameService.finishFrameChange(currentImage);
    }
  }

  changeActiveFrame() {
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].removeAvatar();
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].unlockShape();
    const index = this.componentControllers[DEPENDANTS_ENUM.FRAME_CONTROLLER].currentFrame.number;
    this.frameService.setCurrentFrame(index - 1);
  }

  removeFrame() {
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].removeAvatar();
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].unlockShape();
    const index = this.componentControllers[DEPENDANTS_ENUM.FRAME_CONTROLLER].deletedIndex;
    this.frameService.deleteFrame(index);
  }

  duplicateFrame() {
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].removeAvatar();
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].unlockShape();
    const index = this.componentControllers[DEPENDANTS_ENUM.FRAME_CONTROLLER].duplicateIndex;
    this.frameService.duplicateFrame(index);
  }

  setCurrentColor(e) {
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].removeAvatar();
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].unlockShape();
    if (e.eventObj.which === 1) {
      this.currentColor = this.primaryColor;
    }
    if (e.eventObj.which === 3) {
      this.currentColor = this.secondaryColor;
    }
  }

  setCurrentTool(toolName) {
    this.componentControllers[DEPENDANTS_ENUM.TOOL_BAR]
      .setCurrentToolOnKeyboardEvent(toolName);
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].removeAvatar();
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].unlockShape();
    this.currentTool = toolName;
  }

  addImage() {
    const {
      image: currentImage,
    } = this.componentControllers[DEPENDANTS_ENUM.CANVAS];
    this.frameService.addImage(currentImage);
  }

  pickColorFromCanvas(coordinates, button) {
    this.frameService.getCurrentColor(coordinates, button);
  }

  paintAllPixelsOfTheSameColor(e) {
    if (e.eventObj.which === 1) {
      this.frameService.paintAllPixelsOfTheSameColor(e.coordinates, this.primaryColor);
    } else if (e.eventObj.which === 3) {
      this.frameService.paintAllPixelsOfTheSameColor(e.coordinates, this.secondaryColor);
    }
  }

  applyPaintBucket(e) {
    if (e.eventObj.which === 1) {
      this.frameService.paintColorSegment(e.coordinates, this.primaryColor);
    } else if (e.eventObj.which === 3) {
      this.frameService.paintColorSegment(e.coordinates, this.secondaryColor);
    }
  }

  applyLighten(e) {
    if (e.eventObj.shiftKey) {
      this.frameService.changeSquareColor(e.coordinates, DARKEN);
    } else {
      this.frameService.changeSquareColor(e.coordinates, LIGHTEN);
    }
  }

  applyShapeSelection(e) {
    this.frameService.selectShape(e.coordinates);
  }

  applyFlip(e) {
    if (e.eventObj.shiftKey) {
      this.frameService.flipFrameVertically();
    } else if (e.eventObj.ctrlKey) {
      this.frameService.flipFrameDiagonally();
    } else {
      this.frameService.flipFrameHorizontally();
    }
  }

  applyRotate(e) {
    if (e.eventObj.shiftKey) {
      this.frameService.rotateFrametoRight();
    } else {
      this.frameService.rotateFrametoLeft();
    }
  }

  updateFramesOrder(e) {
    this.frameService.updateFramesOrder(e.eventObj);
  }

  moveLayerUp(e) {
    this.frameService.moveLayerUp(e.data);
  }

  moveLayerDown(e) {
    this.frameService.moveLayerDown(e.data);
  }

  handleCanvasClick(e) {
    switch (this.currentTool) {
      case BUTTON_ENUM.COLOR_PICKER:
        if (e.eventObj.which === 1) {
          this.pickColorFromCanvas(e.coordinates, 1);
        } else if (e.eventObj.which === 3) {
          this.pickColorFromCanvas(e.coordinates, 3);
        }
        break;
      case BUTTON_ENUM.PAINT_ALL_BUCKET:
        this.paintAllPixelsOfTheSameColor(e);
        break;
      case BUTTON_ENUM.PAINT_BUCKET:
        this.applyPaintBucket(e);
        break;
      case BUTTON_ENUM.LIGHTEN:
        this.applyLighten(e);
        break;
      case BUTTON_ENUM.SHAPE_SELECTION:
        this.applyShapeSelection(e);
        break;
      case BUTTON_ENUM.FLIP:
        this.applyFlip(e);
        break;
      case BUTTON_ENUM.ROTATE:
        this.applyRotate(e);
        break;
      default:
        break;
    }
  }

  handleCanvasMouseDown(e) {
    switch (this.currentTool) {
      case BUTTON_ENUM.PEN:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].lockCanvas(e.eventObj);
        break;
      case BUTTON_ENUM.ERASER:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].lockCanvasWithEraser(e.eventObj);
        break;
      case BUTTON_ENUM.MIRROR_PEN:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .lockCanvasWithDoubleCoords(e.eventObj);
        break;
      case BUTTON_ENUM.STROKE:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .startStroke(e.eventObj);
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .lockCanvasWithStroke(e.eventObj);
        break;
      case BUTTON_ENUM.RECTANGLE:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .startStroke(e.eventObj);
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .lockCanvasWithStroke(e.eventObj);
        break;
      case BUTTON_ENUM.CIRCLE:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .startStroke(e.eventObj);
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .lockCanvasWithStroke(e.eventObj);
        break;
      case BUTTON_ENUM.LIGHTEN:
        if (e.eventObj.shiftKey) {
          this.componentControllers[DEPENDANTS_ENUM.CANVAS]
            .lockCanvasWithLighten(e.eventObj, DARKEN, this.frameService.getCurrentFrame());
        } else {
          this.componentControllers[DEPENDANTS_ENUM.CANVAS]
            .lockCanvasWithLighten(e.eventObj, LIGHTEN, this.frameService.getCurrentFrame());
        }
        break;
      case BUTTON_ENUM.DITHERING:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .lockCanvasWithDithering(e.eventObj);
        break;
      default:
        break;
    }
    this.refreshFrame(e);
  }

  handleCanvasMouseMove(e) {
    switch (this.currentTool) {
      case BUTTON_ENUM.PEN:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].applyPenToCanvas(e.eventObj);
        this.refreshFrame(e);
        break;
      case BUTTON_ENUM.ERASER:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].eraseCanvas(e.eventObj);
        this.refreshFrame(e);
        break;
      case BUTTON_ENUM.MIRROR_PEN:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].applyMirrorPenToCanvas(e.eventObj);
        this.refreshFrame(e);
        break;
      case BUTTON_ENUM.STROKE:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].applyStrokeToCanvas(e.eventObj);
        break;
      case BUTTON_ENUM.RECTANGLE:
        if (e.eventObj.shiftKey) {
          this.componentControllers[DEPENDANTS_ENUM.CANVAS].applySquareToCanvas(e.eventObj);
        } else {
          this.componentControllers[DEPENDANTS_ENUM.CANVAS].applyRectangleToCanvas(e.eventObj);
        }
        break;
      case BUTTON_ENUM.CIRCLE:
        if (e.eventObj.shiftKey) {
          this.componentControllers[DEPENDANTS_ENUM.CANVAS].applyCircleToCanvas(e.eventObj);
        } else {
          this.componentControllers[DEPENDANTS_ENUM.CANVAS].applyEllipseToCanvas(e.eventObj);
        }
        break;
      case BUTTON_ENUM.LIGHTEN:
        if (e.eventObj.shiftKey) {
          this.componentControllers[DEPENDANTS_ENUM.CANVAS]
            .applyLightenToCanvas(e.eventObj, DARKEN, this.frameService.getCurrentFrame());
        } else {
          this.componentControllers[DEPENDANTS_ENUM.CANVAS]
            .applyLightenToCanvas(e.eventObj, LIGHTEN, this.frameService.getCurrentFrame());
        }
        this.applyLighten(e);
        break;
      case BUTTON_ENUM.DITHERING:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .applyDitheringToCanvas(e.eventObj);
        switch (e.eventObj.which) {
          case 1:
            this.ditherFrame(e, this.primaryColor, this.secondaryColor);
            break;
          case 3:
            this.ditherFrame(e, this.secondaryColor, this.primaryColor);
            break;
          default:
            break;
        }
        break;
      default:
        break;
    }
  }

  clearFrameSection(e) {
    this.frameService.clearFrameSection(e);
  }

  lockModalWidnow(val) {
    this.windowLocked = val;
    this.componentControllers[DEPENDANTS_ENUM.TOOL_BAR].windowLocked = val;
    this.componentControllers[DEPENDANTS_ENUM.FRAME_CONTROLLER].windowLocked = val;
    this.componentControllers[DEPENDANTS_ENUM.MODAL_DIALOG].windowLocked = val;
    this.componentControllers[DEPENDANTS_ENUM.LAYER_COMPONENT].windowLocked = val;
    this.componentControllers[DEPENDANTS_ENUM.PREVIEW].windowLocked = val;
  }

  addNewLayer() {
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].removeAvatar();
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].unlockShape();
    this.frameService.addNewLayer();
  }

  removeLayer(index) {
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].removeAvatar();
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].unlockShape();
    this.frameService.removeLayer(index);
  }

  resizeCanvas(value) {
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].removeAvatar();
    this.componentControllers[DEPENDANTS_ENUM.CANVAS].unlockShape();
    this.frameService.resizeCanvas(value);
    this.numberOfSquares = value;
  }

  handleAvatarClear(e) {
    switch (this.currentTool) {
      case BUTTON_ENUM.MOVE:
        this.clearFrameSection(e);
        break;
      case BUTTON_ENUM.SHAPE_SELECTION:
        this.frameService.addShapeToFrame(e);
        break;
      default:
        break;
    }
  }

  notifyChange(e) {
    switch (e.type) {
      case EVENT_ENUM.CANVAS_MOUSEDOWN:
        this.handleCanvasMouseDown(e);
        break;
      case EVENT_ENUM.CANVAS_MOUSEMOVE:
        this.handleCanvasMouseMove(e);
        break;
      case EVENT_ENUM.CANVAS_MOUSELEAVE:
        this.drawFrame();
        break;
      case EVENT_ENUM.CANVAS_MOUSEUP:
        if (this.currentTool === BUTTON_ENUM.STROKE
          || this.currentTool === BUTTON_ENUM.RECTANGLE
          || this.currentTool === BUTTON_ENUM.CIRCLE) {
          this.refreshFrame(e);
        }
        if (!this.componentControllers[DEPENDANTS_ENUM.CANVAS].drawn
          && (this.currentTool === BUTTON_ENUM.PEN
          || this.currentTool === BUTTON_ENUM.MIRROR_PEN)) {
          this.refreshFrame(e);
        }
        if (this.currentTool === BUTTON_ENUM.LIGHTEN) {
          this.applyLighten(e);
        }
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].drawn = false;
        this.drawFrame();
        break;
      case EVENT_ENUM.FRAME_BUTTONCLICK:
        this.renderFrame();
        break;
      case EVENT_ENUM.FRAME_CANVAS_CLICK:
        this.changeActiveFrame();
        break;
      case EVENT_ENUM.DELETE_CLICK:
        this.removeFrame();
        break;
      case EVENT_ENUM.DUPLICATE_CLICK:
        this.duplicateFrame();
        break;
      case EVENT_ENUM.COLOR_PICKER_CLICK:
        this.setCurrentTool(BUTTON_ENUM.COLOR_PICKER);
        break;
      case EVENT_ENUM.PRIMARY_COLOR_CHANGE:
        this.primaryColor = this.componentControllers[DEPENDANTS_ENUM.COLOR_PICKER].primaryColor;
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].primaryColor = this.primaryColor;
        break;
      case EVENT_ENUM.SECONDARY_COLOR_CHANGE:
        this.secondaryColor = this.componentControllers[DEPENDANTS_ENUM.COLOR_PICKER]
          .secondaryColor;
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].secondaryColor = this.secondaryColor;
        break;
      case EVENT_ENUM.ADD_EMPTY_FRAME:
        this.addImage();
        break;
      case EVENT_ENUM.IMAGE_UPDATE:
        this.drawFrame();
        break;
      case EVENT_ENUM.PEN_SIZE_CHANGE:
        this.componentControllers[DEPENDANTS_ENUM.CANVAS].setPenSize(
          this.componentControllers[DEPENDANTS_ENUM.TOOL_BAR].currentPenSize,
        );
        break;
      case EVENT_ENUM.CANVAS_CLICK:
        this.handleCanvasClick(e);
        break;
      case EVENT_ENUM.PEN_CLICK:
        this.setCurrentTool(BUTTON_ENUM.PEN);
        break;
      case EVENT_ENUM.ERASER_CLICK:
        this.setCurrentTool(BUTTON_ENUM.ERASER);
        break;
      case EVENT_ENUM.PAINT_ALL_BUCKET_CLICK:
        this.setCurrentTool(BUTTON_ENUM.PAINT_ALL_BUCKET);
        break;
      case EVENT_ENUM.MIRROR_PEN_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.MIRROR_PEN);
        break;
      case EVENT_ENUM.PAINT_BUCKET_CLICK:
        this.setCurrentTool(BUTTON_ENUM.PAINT_BUCKET);
        break;
      case EVENT_ENUM.STROKE_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.STROKE);
        break;
      case EVENT_ENUM.MOVE_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.MOVE);
        this.componentControllers[DEPENDANTS_ENUM.CANVAS]
          .lockCanvasWithMove();
        break;
      case EVENT_ENUM.AVATAR_CLEAR:
        this.handleAvatarClear(e);
        break;
      case EVENT_ENUM.RECTANGLE_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.RECTANGLE);
        break;
      case EVENT_ENUM.CIRCLE_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.CIRCLE);
        break;
      case EVENT_ENUM.DITHERING_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.DITHERING);
        break;
      case EVENT_ENUM.LIGHTEN_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.LIGHTEN);
        break;
      case EVENT_ENUM.SHAPE_SELECTION_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.SHAPE_SELECTION);
        break;
      case EVENT_ENUM.FLIP_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.FLIP);
        break;
      case EVENT_ENUM.ROTATE_BUTTON_CLICK:
        this.setCurrentTool(BUTTON_ENUM.ROTATE);
        break;
      case EVENT_ENUM.FRAME_SORT:
        this.updateFramesOrder(e);
        break;
      case EVENT_ENUM.MODAL_WINDOW_LOCKED:
        this.lockModalWidnow(true);
        break;
      case EVENT_ENUM.MODAL_WINDOW_UNLOCKED:
        this.lockModalWidnow(false);
        break;
      case LAYER_EVENT_ENUM.ADD_LAYER:
        this.addNewLayer();
        break;
      case LAYER_EVENT_ENUM.REMOVE_LAYER:
        this.removeLayer(e.data);
        break;
      case LAYER_EVENT_ENUM.MOVE_LAYER_UP:
        this.moveLayerUp(e);
        break;
      case LAYER_EVENT_ENUM.MOVE_LAYER_DOWN:
        this.moveLayerDown(e);
        break;
      case SETTINGS_EVENT_ENUM.RESIZE_BUTTON_CLICK:
        this.resizeCanvas(e.data);
        break;
      case LAYER_EVENT_ENUM.CHANGE_CURRENT_LAYER:
        this.frameService.changeCurrentLayer(e.data);
        break;
      case EVENT_ENUM.IMAGES_UPDATE:
        this.frameService.updateAllImages(e.eventObj);
        break;
      case SETTINGS_EVENT_ENUM.COORDINATES_CHANGE:
        this.componentControllers[DEPENDANTS_ENUM.SETTINGS_BAR]
          .updateCoords(e.data);
        break;
      case SETTINGS_EVENT_ENUM.CANVAS_MOUSE_LEAVE:
        this.componentControllers[DEPENDANTS_ENUM.SETTINGS_BAR]
          .clearCoordinates();
        break;
      case SETTINGS_EVENT_ENUM.FILE_UPLOAD:
        this.loadNewFile(e.data);
        break;
      case EVENT_ENUM.RATE_CHANGE:
        this.componentControllers[DEPENDANTS_ENUM.SETTINGS_BAR].rate = e.eventObj.rate;
        break;
      case EVENT_ENUM.CANVAS_AVATAR_ROLLBACK:
        this.frameService.restoreShapeState();
        break;
      default:
        break;
    }
  }

  handleKeypressEvent(e) {
    const {
      key, ctrlKey, shiftKey, altKey,
    } = e;
    const eventValue = JSON.stringify({
      key: key.toUpperCase(), altKey, shiftKey, ctrlKey,
    });
    const property = this.keyboardService.checkShortcutValue(eventValue);
    if (property.length > 0) {
      e.preventDefault();
      const values = Object.values(KEYBOARD_SHORTCUTS_PROPERTIES_ENUM);
      if (values.indexOf(property[0][0]) > -1) {
        switch (property[0][0]) {
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.PEN:
            this.setCurrentTool(BUTTON_ENUM.PEN);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.ERASER:
            this.setCurrentTool(BUTTON_ENUM.ERASER);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.PAINT_ALL_BUCKET:
            this.setCurrentTool(BUTTON_ENUM.PAINT_ALL_BUCKET);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.MIRROR_PEN:
            this.setCurrentTool(BUTTON_ENUM.MIRROR_PEN);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.PAINT_BUCKET:
            this.setCurrentTool(BUTTON_ENUM.PAINT_BUCKET);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.STROKE:
            this.setCurrentTool(BUTTON_ENUM.STROKE);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.MOVE:
            this.setCurrentTool(BUTTON_ENUM.MOVE);
            this.componentControllers[DEPENDANTS_ENUM.CANVAS]
              .lockCanvasWithMove();
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.RECTANGLE:
            this.setCurrentTool(BUTTON_ENUM.RECTANGLE);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.CIRCLE:
            this.setCurrentTool(BUTTON_ENUM.CIRCLE);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.DITHERING:
            this.setCurrentTool(BUTTON_ENUM.DITHERING);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.LIGHTEN:
            this.setCurrentTool(BUTTON_ENUM.LIGHTEN);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.SHAPE_SELECTION:
            this.setCurrentTool(BUTTON_ENUM.SHAPE_SELECTION);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.FLIP:
            this.setCurrentTool(BUTTON_ENUM.FLIP);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.ROTATE:
            this.setCurrentTool(BUTTON_ENUM.ROTATE);
            break;
          case KEYBOARD_SHORTCUTS_PROPERTIES_ENUM.COLOR_PICKER:
            this.setCurrentTool(BUTTON_ENUM.COLOR_PICKER);
            break;
          default:
            break;
        }
      }
    }
    return false;
  }

  listen() {
    $('body').on({
      contextmenu: (e) => {
        e.preventDefault();
      },
      keydown: (e) => {
        if (!this.windowLocked) {
          this.handleKeypressEvent(e);
        }
      },
    });
    $(window).on({
      unload: () => this.saveCurrentState(),
    });
  }
}
