import $ from 'jquery';
import FileSaver from 'file-saver';
import ISubject from '../services/ISubject';
import AppSettingsBarView from '../views/appSettingsBarView/AppSettingsBarView';
import SettingsEvent, { SETTINGS_EVENT_ENUM } from '../models/AppSettingsEvent';
import AppFilesaverService from '../services/AppFilesaverService';
import { EVENT_ENUM } from '../models/AppEvent';
// import { MODEL_EVENT_ENUM } from '../models/AppModelEvent';

const MIN_VALUE = 32;
const MED_VALUE = 64;
const MAX_VALUE = 128;
const DEFAULT_SIZE = 800;
const DEFAULT_RATE = 12;
const VALUE_BUTTONS = {
  32: 'settings-bar__button--min',
  64: 'settings-bar__button--med',
  128: 'settings-bar__button--large',
};

export default class AppSettingsBarController extends ISubject {
  constructor(keyboardService, fileSaveService) {
    super();
    this.element = new AppSettingsBarView();
    this.numberOfSquares = MIN_VALUE;
    this.currentButton = VALUE_BUTTONS[MIN_VALUE];
    this.keyboardService = keyboardService;
    this.fileSaverService = fileSaveService;
    this.fileName = '';
    this.size = DEFAULT_SIZE;
    this.rate = DEFAULT_RATE;
  }

  init(container) {
    this.element.render(container);
    this.setCanvasInfo(this.numberOfSquares);
    $(`#${this.currentButton} label`).addClass('ui-state-active');
  }

  setInitialState(initialState) {
    const { numberOfSquares } = initialState;
    this.numberOfSquares = numberOfSquares;
    this.currentButton = VALUE_BUTTONS[numberOfSquares];
  }

  setInitialFileState(numberOfSquares) {
    this.numberOfSquares = numberOfSquares;
    this.currentButton = VALUE_BUTTONS[numberOfSquares];
    $(`#${this.element.canvasInfoId}`).html(`[ ${this.numberOfSquares} x ${this.numberOfSquares} ]`);
  }

  setFrameInfo(eventData) {
    const { currentFrame, numberOfFrames } = eventData;
    $(`#${this.element.frameInfoId}`).html(`${currentFrame + 1} / ${numberOfFrames}`);
  }

  notifyChange(event) {
    if (event.type === SETTINGS_EVENT_ENUM.MODEL_STRUCTURE_CHANGE) {
      this.setFrameInfo(event.data);
    }
  }

  setCanvasInfo(value) {
    this.numberOfSquares = value;
    $(`#${this.element.canvasInfoId}`).html(`[ ${this.numberOfSquares} x ${this.numberOfSquares} ]`);
  }

  changeCanvasSize(value) {
    const event = new SettingsEvent(SETTINGS_EVENT_ENUM.RESIZE_BUTTON_CLICK, value);
    this.notify(event);
    this.numberOfSquares = value;
    $(`#${this.element.canvasInfoId}`).html(`[ ${value} x ${value} ]`);
  }

  updateCoords(coords) {
    const { indexX, indexY } = coords;
    $(`#${this.element.coordsInfoId}`).html(`${indexX} : ${indexY}`);
  }

  clearCoordinates() {
    $(`#${this.element.coordsInfoId}`).html('');
  }

  setInitialExportSettings() {
    const lockEvent = new Event(EVENT_ENUM.MODAL_WINDOW_LOCKED);
    this.notify(lockEvent);
    this.fileName = '';
    $(`#${this.element.exportsettingsNameId}`).on({
      input: (event) => {
        this.fileName = event.target.value;
      },
    });
    $(`#${this.element.exportsettingsSizeId}`).on({
      input: (event) => {
        this.size = +event.target.value;
      },
    });
  }

  removeEventListeners() {
    $(`#${this.element.exportSettingsDialogSubmitButtonId}`).off('click');
  }

  handleExportasGif() {
    $(`#${this.element.exportsettingsSizeId}`).show();
    $(`#${this.element.exportsettingsNameId}`).attr('placeholder', 'newGif');
    this.setInitialExportSettings();
    this.removeEventListeners();
    $(`#${this.element.exportSettingsDialogSubmitButtonId}`).on({
      click: () => {
        if (!this.fileName) {
          this.fileName = 'NewGif';
        }
        const gif = this.fileSaverService
          .convertPawskelToGif(this.numberOfSquares, this.size, this.rate);
        gif.on('finished', (blob) => {
          const fileToSave = new File([blob], 'newGif.gif', { type: 'image/gif;charset=utf-8' });
          FileSaver.saveAs(fileToSave);
          $(`#${this.element.exportSettingsDialogId}`).dialog('close');
          const event = new Event(EVENT_ENUM.MODAL_WINDOW_UNLOCKED);
          this.notify(event);
          this.windowLocked = false;
        });
        gif.render();
      },
    });
  }

  handleExportasApng() {
    $(`#${this.element.exportsettingsSizeId}`).show();
    $(`#${this.element.exportsettingsNameId}`).attr('placeholder', 'newApng');
    this.setInitialExportSettings();
    this.removeEventListeners();
    $(`#${this.element.exportSettingsDialogSubmitButtonId}`).on({
      click: () => {
        if (!this.fileName) {
          this.fileName = 'NewApng';
        }
        const apng = this.fileSaverService
          .converPawskelToApng(this.numberOfSquares, this.size, this.rate);
        const fileToSave = new File([apng], `${this.fileName}.apng`, { type: 'image/apng;charset=utf-8' });
        FileSaver.saveAs(fileToSave);
        $(`#${this.element.exportSettingsDialogId}`).dialog('close');
        const event = new Event(EVENT_ENUM.MODAL_WINDOW_UNLOCKED);
        this.notify(event);
        this.windowLocked = false;
      },
    });
  }

  handleExportasPawskel() {
    $(`#${this.element.exportsettingsNameId}`).attr('placeholder', 'newPawskel');
    this.setInitialExportSettings();
    this.removeEventListeners();
    $(`#${this.element.exportsettingsSizeId}`).hide();
    $(`#${this.element.exportSettingsDialogSubmitButtonId}`).on({
      click: () => {
        if (!this.fileName) {
          this.fileName = 'NewPawskel';
        }
        const pawskel = this.fileSaverService.createPawskel(
          this.numberOfSquares, this.fileName,
        );
        const pawskelBlob = AppFilesaverService.convertPawskelToPawskel(pawskel);
        FileSaver.saveAs(pawskelBlob, `${this.fileName}.pawskel`);
        $(`#${this.element.exportSettingsDialogId}`).dialog('close');
        const event = new Event(EVENT_ENUM.MODAL_WINDOW_UNLOCKED);
        this.notify(event);
        this.windowLocked = false;
      },
    });
  }

  openPawskelFile(inputData) {
    const currentFiles = inputData;
    if (currentFiles.length === 0) {
      $('#error-message-open').dialog('open');
    } else {
      $('#warning-message').dialog('close');
      const reader = new FileReader();
      const currentFile = currentFiles[0];
      reader.addEventListener('loadend', () => {
        let uploadedFile = reader.result;
        uploadedFile = JSON.parse(uploadedFile);
        this.setInitialFileState(uploadedFile.numberOfSquares);
        const event = new SettingsEvent(SETTINGS_EVENT_ENUM.FILE_UPLOAD, uploadedFile);
        this.notify(event);
      }, false);
      reader.readAsText(currentFile);
    }
  }

  listen() {
    $(`#${this.element.minButtonId}`).on({
      change: () => this.changeCanvasSize(MIN_VALUE),
    });
    $(`#${this.element.medButtonId}`).on({
      change: () => this.changeCanvasSize(MED_VALUE),
    });
    $(`#${this.element.largeButtonId}`).on({
      change: () => this.changeCanvasSize(MAX_VALUE),
    });
    $(`#${this.element.exportAsPawskelId}`).on({
      click: () => {
        this.handleExportasPawskel();
      },
    });
    $(`#${this.element.exportAsApngId}`).on({
      click: () => {
        this.handleExportasApng();
      },
    });
    $(`#${this.element.exportAsGifId}`).on({
      click: () => {
        this.handleExportasGif();
      },
    });
    $(`#${this.element.saveDialogCancelButtonId}`).on({
      click: () => {
        const event = new Event(EVENT_ENUM.MODAL_WINDOW_UNLOCKED);
        this.notify(event);
        this.fileName = '';
        $(`#${this.element.saveDialogId}`).dialog('close');
      },
    });
    $(`#${this.element.exportSettingsDialogId}`).dialog({
      close: () => {
        const event = new Event(EVENT_ENUM.MODAL_WINDOW_UNLOCKED);
        this.notify(event);
      },
    });
    $(`#${this.element.saveDialogId}`).dialog({
      close: () => {
        const event = new Event(EVENT_ENUM.MODAL_WINDOW_UNLOCKED);
        this.notify(event);
        this.fileName = '';
      },
    });
    $(`#${this.element.importPawskelId}`).on({
      click: () => {
        $('#warning-message').dialog('open');
      },
      change: () => {
        const input = document.getElementById(`${this.element.importPawskelId}`);
        this.openPawskelFile(input.files);
      },
    });
  }
}
