import $ from 'jquery';
import './bodyStyles.less';

export default class BodyView {
  constructor() {
    this.cursor = '';
    this.containerId = 'grid-container';
    this.containerTemplate = `<div class="grid-container" id="${this.containerId}"></div>`;
  }

  applyCustomCursor(cursor) {
    this.cursor = cursor;
    $('body').attr('cursor', this.cursor);
  }

  render() {
    $('body').append(this.containerTemplate);
  }
}
