import $ from 'jquery';
import ISubject from '../services/ISubject';
import AppViewCanvas from '../views/appCanvasView/AppCanvasView';
import Event, { EVENT_ENUM } from '../models/AppEvent';
import { MODEL_EVENT_ENUM } from '../models/AppModelEvent';
import ToolCanvasAvatar from '../views/appTools/toolCanvasAvatar/ToolCanvasAvatar';
import { LAYER_EVENT_ENUM } from '../models/AppLayerEvent';
import ToolLayer from '../views/appTools/toolLayer/ToolLayer';
import AppSettingsEvent, { SETTINGS_EVENT_ENUM } from '../models/AppSettingsEvent';

const DEFAULT_PEN_SIZE = 1;

export default class AppCanvasController extends ISubject {
  constructor(numberOfSquares, canvasWidth, baseColor, primaryColor, secondaryColor) {
    super();
    this.numberOfSquares = numberOfSquares;
    this.canvasWidth = canvasWidth;
    this.baseColor = baseColor;
    this.element = null;
    this.mainCanvas = this.element;
    this.draftLayer = null;
    this.layers = [];
    this.canvasAvatar = new ToolCanvasAvatar(
      this.numberOfSquares, this.canvasWidth, this.baseColor,
    );
    this.currentColor = primaryColor;
    this.primaryColor = primaryColor;
    this.secondaryColor = secondaryColor;
    this.locked = false;
    this.strokeLocked = false;
    this.watched = true;
    this.coords = [];
    this.finalCoords = [];
    this.image = null;
    this.images = [];
    this.currentFrame = null;
    this.currentCanvasX = null;
    this.currentCanvasY = null;
    this.penSize = DEFAULT_PEN_SIZE;
    this.initialX = null;
    this.initialY = null;
    this.drawn = false;
    this.coordsMinimized = false;
  }

  init(container) {
    this.element = new AppViewCanvas(this.numberOfSquares, this.canvasWidth, this.baseColor);
    this.element.render(container);
    $(`#${this.element.canvasContainerId}`).css({
      width: `${this.canvasWidth + 1}px`,
      height: `${this.canvasWidth + 1}px`,
    });
    this.image = this.createImage();
    this.layers.push(this.element);
  }

  setInitialState(currentState) {
    const {
      numberOfSquares,
      primaryColor,
      secondaryColor,
      penSize,
    } = currentState;
    this.numberOfSquares = numberOfSquares;
    this.currentColor = primaryColor;
    this.primaryColor = primaryColor;
    this.secondaryColor = secondaryColor;
    this.penSize = penSize;
  }

  setPenSize(size) {
    this.penSize = size;
  }

  paintCanvas(coordinates, color) {
    this.element.paint(coordinates, color);
    this.image = this.createImage();
  }

  changeCanvasLightning(lightness, frame) {
    this.minimizeCoordinates();
    this.element.lighten(this.coords, lightness, frame);
    this.image = this.createImage();
  }

  ditherCanvas(coordinates, primaryColor, secondaryColor) {
    this.element.dither(coordinates, primaryColor, secondaryColor);
    this.image = this.createImage();
  }

  drawPoint(prevX, prevY) {
    const currentCoords = [];
    if (this.penSize >= 1) {
      currentCoords.push({
        indexX: prevX,
        indexY: prevY,
      });
    }
    if (this.penSize >= 2) {
      currentCoords.push(
        { indexX: prevX, indexY: prevY - 1 },
        { indexX: prevX - 1, indexY: prevY - 1 },
        { indexY: prevY, indexX: prevX - 1 },
      );
    }
    if (this.penSize >= 3) {
      currentCoords.push(
        { indexX: prevX, indexY: prevY + 1 },
        { indexY: prevY, indexX: prevX + 1 },
        { indexX: prevX + 1, indexY: prevY + 1 },
        { indexX: prevX + 1, indexY: prevY - 1 },
        { indexX: prevX - 1, indexY: prevY + 1 },
      );
    }
    if (this.penSize === 4) {
      currentCoords.push(
        { indexX: prevX - 2, indexY: prevY - 2 },
        { indexX: prevX - 2, indexY: prevY - 1 },
        { indexX: prevX - 2, indexY: prevY },
        { indexX: prevX - 2, indexY: prevY + 1 },
        { indexX: prevX - 1, indexY: prevY - 2 },
        { indexX: prevX, indexY: prevY - 2 },
        { indexX: prevX + 1, indexY: prevY - 2 },
      );
    }
    for (let i = 0; i < currentCoords.length; i += 1) {
      this.coords.push(currentCoords[i]);
    }
  }

  drawStroke(initX, initY, finalX, finalY) {
    let prevX = initX;
    let prevY = initY;
    const deltaX = Math.abs(finalX - prevX);
    const deltaY = Math.abs(finalY - prevY);
    const signX = prevX < finalX ? 1 : -1;
    const signY = prevY < finalY ? 1 : -1;
    let error = deltaX - deltaY;
    while (finalX !== prevX || prevY !== finalY) {
      const nextError = error * 2;
      if (nextError > -deltaY) {
        error -= deltaY;
        prevX += signX;
      }
      if (nextError < deltaX) {
        error += deltaX;
        prevY += signY;
      }
      this.drawPoint(prevX, prevY);
      this.currentCanvasX = finalX;
      this.currentCanvasY = finalY;
    }
  }

  minimizeCoordinates() {
    if (!this.coordsMinimized) {
      for (let i = 0; i < this.coords.length - 1; i += 1) {
        for (let j = i + 1; j < this.coords.length; j += 1) {
          if (this.coords[i].indexX === this.coords[j].indexX
          && this.coords[i].indexY === this.coords[j].indexY) {
            this.coords.splice(j, 1);
          }
        }
      }
      this.coordsMinimized = true;
    }
  }

  getInitialCoordinates(event) {
    const initialCoords = this.getCoordinates(event);
    this.currentCanvasX = initialCoords[0].indexX;
    this.currentCanvasY = initialCoords[0].indexY;
    [...this.coords] = [...initialCoords];
    [...this.finalCoords] = [...initialCoords];
  }

  getFinalCoordinates(event) {
    const finalCoords = this.getCoordinates(event);
    const prevX = this.currentCanvasX;
    const prevY = this.currentCanvasY;
    const finalX = finalCoords[0].indexX;
    const finalY = finalCoords[0].indexY;
    return {
      prevX,
      prevY,
      finalX,
      finalY,
    };
  }

  getCoordinatesForStroke(event) {
    const {
      prevX,
      prevY,
      finalX,
      finalY,
    } = this.getFinalCoordinates(event);
    if (typeof prevX === 'number' && typeof prevY === 'number') {
      this.drawStroke(prevX, prevY, finalX, finalY);
    }
  }

  getCoordinatesForSquare(event) {
    const {
      prevX,
      prevY,
      finalX,
      finalY,
    } = this.getFinalCoordinates(event);
    if (typeof prevX === 'number' && typeof prevY === 'number') {
      const delta = Math.abs(prevX - finalX) - Math.abs(prevY - finalY);
      if (delta > 0) {
        if (prevX > finalX) {
          this.drawStroke(prevX, prevY, prevX, finalY);
          this.drawStroke(prevX, finalY, finalX + delta, finalY);
          this.drawStroke(finalX + delta, finalY, finalX + delta, prevY);
          this.drawStroke(finalX + delta, prevY, prevX, prevY);
        } else {
          this.drawStroke(prevX, prevY, prevX, finalY);
          this.drawStroke(prevX, finalY, finalX - delta, finalY);
          this.drawStroke(finalX - delta, finalY, finalX - delta, prevY);
          this.drawStroke(finalX - delta, prevY, prevX, prevY);
        }
      } else if (prevY < finalY) {
        this.drawStroke(prevX, prevY, prevX, finalY + delta);
        this.drawStroke(prevX, finalY + delta, finalX, finalY + delta);
        this.drawStroke(finalX, finalY + delta, finalX, prevY);
        this.drawStroke(finalX, prevY, prevX, prevY);
      } else {
        this.drawStroke(prevX, prevY, prevX, finalY - delta);
        this.drawStroke(prevX, finalY - delta, finalX, finalY - delta);
        this.drawStroke(finalX, finalY - delta, finalX, prevY);
        this.drawStroke(finalX, prevY, prevX, prevY);
      }
    }
  }

  getCoordinatesForRectangle(event) {
    const {
      prevX,
      prevY,
      finalX,
      finalY,
    } = this.getFinalCoordinates(event);
    if (typeof prevX === 'number' && typeof prevY === 'number') {
      this.drawStroke(prevX, prevY, prevX, finalY);
      this.drawStroke(prevX, finalY, finalX, finalY);
      this.drawStroke(finalX, finalY, finalX, prevY);
      this.drawStroke(finalX, prevY, prevX, prevY);
    }
  }

  getCoordinates(event) {
    const elementOffset = $(`#${this.element.elementId}`).offset();
    const {
      left: elementX,
      top: elementY,
    } = elementOffset;
    const {
      pageX: currentX,
      pageY: currentY,
    } = event;
    const canvasX = Math.abs(currentX - elementX);
    const canvasY = Math.abs(currentY - elementY);
    const indexX = Math.floor(canvasX / this.element.squareWidth);
    const indexY = Math.floor(canvasY / this.element.squareWidth);
    if (this.penSize === 1) {
      return [{
        indexX,
        indexY,
      }];
    }
    if (this.penSize === 2) {
      return [
        { indexX, indexY },
        { indexX, indexY: indexY - 1 },
        { indexX: indexX - 1, indexY: indexY - 1 },
        { indexY, indexX: indexX - 1 },
      ];
    }
    if (this.penSize === 3) {
      return [
        { indexX, indexY },
        { indexX, indexY: indexY - 1 },
        { indexX, indexY: indexY + 1 },
        { indexX: indexX - 1, indexY: indexY - 1 },
        { indexY, indexX: indexX - 1 },
        { indexY, indexX: indexX + 1 },
        { indexX: indexX + 1, indexY: indexY + 1 },
        { indexX: indexX + 1, indexY: indexY - 1 },
        { indexX: indexX - 1, indexY: indexY + 1 },
      ];
    }
    return [
      { indexX, indexY },
      { indexX, indexY: indexY - 1 },
      { indexX, indexY: indexY + 1 },
      { indexX: indexX - 1, indexY: indexY - 1 },
      { indexY, indexX: indexX - 1 },
      { indexY, indexX: indexX + 1 },
      { indexX: indexX + 1, indexY: indexY + 1 },
      { indexX: indexX + 1, indexY: indexY - 1 },
      { indexX: indexX - 1, indexY: indexY + 1 },
      { indexX: indexX - 2, indexY: indexY - 2 },
      { indexX: indexX - 2, indexY: indexY - 1 },
      { indexX: indexX - 2, indexY },
      { indexX: indexX - 2, indexY: indexY + 1 },
      { indexX: indexX - 1, indexY: indexY - 2 },
      { indexX, indexY: indexY - 2 },
      { indexX: indexX + 1, indexY: indexY - 2 },
    ];
  }

  getPointIndex(point) {
    return Math.floor(point / this.element.squareWidth);
  }

  setCircleCoordinates(centerX, centerY, radius) {
    let x = 0;
    let y = radius;
    let delta = 1 - 2 * radius;
    let error = 0;
    while (y >= 0) {
      this.drawPoint(centerX + x, centerY + y);
      this.drawPoint(centerX + x, centerY - y);
      this.drawPoint(centerX - x, centerY + y);
      this.drawPoint(centerX - x, centerY - y);
      if (delta < 0 && error <= 0) {
        x += 1;
        delta += 2 * x + 1;
      } else {
        error = 2 * (delta - x) - 1;
        if (delta > 0 && error > 0) {
          y -= 1;
          delta += 1 - 2 * y;
        } else {
          x += 1;
          delta += 2 * (x - y);
          y -= 1;
        }
      }
    }
  }

  setEllipseCoordinates(centerX, centerY, radiusMain, radiusSec) {
    let x = 0;
    let y = radiusSec;
    const radiusMainSqrt = radiusMain * radiusMain;
    const radiusSecSqrt = radiusSec * radiusSec;
    let delta = 4 * radiusSecSqrt * ((x + 1) * (x + 1)) + radiusMainSqrt
    * ((2 * y - 1) * (2 * y - 1)) - 4 * radiusMainSqrt * radiusSecSqrt;
    while (radiusMainSqrt * (2 * y - 1) > 2 * radiusSecSqrt * (x + 1)) {
      this.drawPoint(centerX + x, centerY + y);
      this.drawPoint(centerX + x, centerY - y);
      this.drawPoint(centerX - x, centerY + y);
      this.drawPoint(centerX - x, centerY - y);
      if (delta < 0) {
        x += 1;
        delta += 4 * radiusSecSqrt * (2 * x + 3);
      } else {
        x += 1;
        delta = delta - 8 * radiusMainSqrt * (y - 1) + 4 * radiusSecSqrt * (2 * x + 3);
        y -= 1;
      }
    }
    delta = radiusSecSqrt * ((2 * x + 1) * (2 * x + 1)) + 4 * radiusMainSqrt
    * ((y + 1) * (y + 1)) - 4 * radiusMainSqrt * radiusSecSqrt;
    while (y + 1 !== 0) {
      this.drawPoint(centerX + x, centerY + y);
      this.drawPoint(centerX + x, centerY - y);
      this.drawPoint(centerX - x, centerY + y);
      this.drawPoint(centerX - x, centerY - y);
      if (delta < 0) {
        y -= 1;
        delta += 4 * radiusMainSqrt * (2 * y + 3);
      } else {
        y -= 1;
        delta = delta - 8 * radiusSecSqrt * (x + 1) + 4 * radiusMainSqrt * (2 * y + 3);
        x += 1;
      }
    }
  }

  getCoordinatesForEllipse(event) {
    const {
      prevX,
      prevY,
      finalX,
      finalY,
    } = this.getFinalCoordinates(event);
    if (typeof prevX === 'number' && typeof prevY === 'number') {
      let centerX = Math.ceil(Math.abs(prevX - finalX) / 2);
      let centerY = Math.ceil(Math.abs(prevY - finalY) / 2);
      const radiusMain = centerX;
      const radiusSec = centerY;
      if (prevX > finalX) {
        centerX = prevX - centerX;
      } else {
        centerX += prevX;
      }
      if (prevY > finalY) {
        centerY = prevY - centerY;
      } else {
        centerY += prevY;
      }
      this.setEllipseCoordinates(centerX, centerY, radiusMain, radiusSec);
    }
  }

  getCoordinatesForCircle(event) {
    const {
      prevX,
      prevY,
      finalX,
      finalY,
    } = this.getFinalCoordinates(event);
    if (typeof prevX === 'number' && typeof prevY === 'number') {
      let centerX = Math.ceil(Math.abs(prevX - finalX) / 2);
      let centerY = Math.ceil(Math.abs(prevY - finalY) / 2);
      let radius;
      let delta;
      if (centerX > centerY) {
        radius = centerY;
        delta = centerX - centerY;
        if (prevX > finalX) {
          centerX = prevX - centerX + delta;
        } else {
          centerX += prevX - delta;
        }
        if (prevY > finalY) {
          centerY = prevY - centerY;
        } else {
          centerY += prevY;
        }
      } else {
        radius = centerX;
        delta = centerY - centerX;
        if (prevX > finalX) {
          centerX = prevX - centerX;
        } else {
          centerX += prevX;
        }
        if (prevY > finalY) {
          centerY = prevY - centerY + delta;
        } else {
          centerY += prevY - delta;
        }
      }
      this.setCircleCoordinates(centerX, centerY, radius);
    }
  }

  clearCanvas() {
    this.layers.forEach(layer => layer.clear());
  }

  drawCurrentFrame(layers) {
    this.images = [];
    layers.forEach((layer, index) => {
      this.layers[index].drawNewFrame(layer);
      this.images.push(this.layers[index].createImage());
    });
    this.image = this.element.createImage();
  }

  setCurrentColor(event) {
    if (event.which === 1) {
      this.currentColor = this.primaryColor;
    }
    if (event.which === 3) {
      this.currentColor = this.secondaryColor;
    }
  }

  createImage() {
    return this.element.createImage();
  }

  doubleCoords() {
    const currentLength = this.coords.length;
    for (let i = 0; i < currentLength; i += 1) {
      const currentCoords = this.coords[i];
      const {
        indexX: x,
        indexY: y,
      } = currentCoords;
      this.coords.push({
        indexX: this.numberOfSquares - x - 1,
        indexY: y,
      });
      this.finalCoords.push({
        indexX: this.numberOfSquares - x - 1,
        indexY: y,
      });
    }
  }

  startStroke() {
    if (!this.strokeLocked) {
      this.finalCoords = [];
      this.strokeLocked = true;
    }
  }

  lockCanvas(event) {
    this.locked = true;
    this.getInitialCoordinates(event);
    this.setCurrentColor(event);
    this.paintCanvas(this.coords, this.currentColor);
  }

  lockCanvasWithDoubleCoords(event) {
    this.lockCanvas(event);
    this.doubleCoords();
    this.paintCanvas(this.coords, this.currentColor);
  }

  lockCanvasWithEraser(event) {
    this.lockCanvas(event);
    this.element.erase(this.coords, this.currentColor);
  }

  lockCanvasWithStroke(event) {
    if (this.strokeLocked) {
      this.locked = true;
      this.getInitialCoordinates(event);
      this.initialX = this.coords[0].indexX;
      this.initialY = this.coords[0].indexY;
      this.setCurrentColor(event);
      this.element.saveCurrentState();
      this.strokeLocked = false;
    }
  }

  lockCanvasWithLighten(event, lightness, frame) {
    this.locked = true;
    this.getInitialCoordinates(event);
    this.changeCanvasLightning(lightness, frame);
  }

  lockCanvasWithDithering(event) {
    this.locked = true;
    this.getInitialCoordinates(event);
    if (event.which === 1) {
      this.ditherCanvas(this.coords, this.primaryColor, this.secondaryColor);
    }
    if (event.which === 3) {
      this.ditherCanvas(this.coords, this.secondaryColor, this.primaryColor);
    }
  }

  copyCanvas() {
    this.element.saveCurrentState();
    const canvas = document.getElementById(this.canvasAvatar.elementId);
    const ctx = canvas.getContext('2d');
    ctx.putImageData(this.element.currentState, 0, 0);
    const mainCanvas = document.getElementById(this.element.elementId);
    const ctxCanvas = mainCanvas.getContext('2d');
    ctxCanvas.clearRect(0, 0, this.canvasWidth, this.canvasWidth);
  }

  lockCanvasWithMove() {
    this.canvasAvatar.render(this.element.canvasContainerId);
    const offset = $(`#${this.layers[0].elementId}`).offset();
    const {
      top,
      left,
    } = offset;
    $(`#${this.canvasAvatar.elementId}`).offset({ top, left });
    this.refreshLayersOffset();
    this.listenToCanvasAvatar();
    $(`#${this.canvasAvatar.elementId}`).css('cursor', 'grab');
  }

  lockAvatar(event) {
    if (this.canvasAvatar.locked === false) {
      this.canvasAvatar.locked = true;
      if (this.canvasAvatar.shapeLocked === false) {
        this.copyCanvas();
      }
      this.canvasAvatar.shapeApplied = false;
      const {
        pageX: currentX,
        pageY: currentY,
      } = event;
      this.canvasAvatar.initialCoords = {
        currentX,
        currentY,
      };
    } else {
      this.canvasAvatar.locked = false;
    }
  }

  handleAvatarMove(event) {
    if (this.canvasAvatar.locked) {
      const {
        pageX: currentX,
        pageY: currentY,
      } = event;
      const distanceX = currentX - this.canvasAvatar.initialCoords.currentX;
      const distanceY = currentY - this.canvasAvatar.initialCoords.currentY;
      this.canvasAvatar.marginLeft += distanceX;
      this.canvasAvatar.marginTop += distanceY;
      $(`#${this.canvasAvatar.elementId}`).css({
        'margin-top': `${this.canvasAvatar.marginTop}px`,
        'margin-left': `${this.canvasAvatar.marginLeft}px`,
      });
      this.canvasAvatar.initialCoords = { currentX, currentY };
    }
  }

  unlockShape() {
    this.canvasAvatar.shapeLocked = false;
  }

  refreshLayersOffset() {
    const offset = $(`#${this.layers[0].elementId}`).offset();
    const {
      top,
      left,
    } = offset;
    this.layers.forEach(layer => $(`#${layer.elementId}`).offset({ top, left }));
  }

  removeAvatar() {
    if (this.canvasAvatar && $('body').has(`#${this.canvasAvatar.elementId}`).length) {
      this.canvasAvatar.clear();
      if (!this.canvasAvatar.shapeApplied) {
        const canvasEvent = new Event(EVENT_ENUM.CANVAS_AVATAR_ROLLBACK);
        this.notify(canvasEvent);
      }
      this.refreshLayersOffset();
    }
  }

  unlockAvatar(event) {
    $(`#${this.canvasAvatar.elementId}`).css('cursor', 'grab');
    this.canvasAvatar.locked = false;
    if (event.shiftKey
    || !this.canvasAvatar.shapeLocked
    || (event.key === 'Enter' && this.canvasAvatar.shapeLocked)) {
      const squareWidth = this.canvasWidth / this.numberOfSquares;
      this.canvasAvatar.marginTop = Math.round(this.canvasAvatar.marginTop / squareWidth)
      * squareWidth;
      this.canvasAvatar.marginLeft = Math.round(this.canvasAvatar.marginLeft / squareWidth)
      * squareWidth;
      $(`#${this.canvasAvatar.elementId}`).css({
        'margin-top': `${this.canvasAvatar.marginTop}px`,
        'margin-left': `${this.canvasAvatar.marginLeft}px`,
      });
      const e = new Event(EVENT_ENUM.AVATAR_CLEAR, {
        x: this.canvasAvatar.marginLeft / squareWidth,
        y: this.canvasAvatar.marginTop / squareWidth,
      });
      this.notify(e);
      this.canvasAvatar.clear();
      if (!this.canvasAvatar.shapeLocked) {
        this.lockCanvasWithMove();
      }
      this.canvasAvatar.shapeApplied = true;
    }
  }

  applyPenToCanvas(event) {
    this.setCurrentColor(event);
    this.getCoordinatesForStroke(event);
    this.minimizeCoordinates();
    this.paintCanvas(this.coords, this.currentColor);
  }

  applyMirrorPenToCanvas(event) {
    this.setCurrentColor(event);
    this.getCoordinatesForStroke(event);
    this.minimizeCoordinates();
    this.doubleCoords();
    this.paintCanvas(this.coords, this.currentColor);
  }

  prepareForDrawingShapes(event) {
    this.element.restoreInitialState();
    this.setCurrentColor(event);
    this.currentCanvasX = this.initialX;
    this.currentCanvasY = this.initialY;
  }

  applyStrokeToCanvas(event) {
    this.prepareForDrawingShapes(event);
    this.getCoordinatesForStroke(event);
    this.minimizeCoordinates();
    this.paintCanvas(this.coords, this.currentColor);
  }

  applyRectangleToCanvas(event) {
    this.prepareForDrawingShapes(event);
    this.getCoordinatesForRectangle(event);
    this.minimizeCoordinates();
    this.paintCanvas(this.coords, this.currentColor);
  }

  applySquareToCanvas(event) {
    this.prepareForDrawingShapes(event);
    this.getCoordinatesForSquare(event);
    this.minimizeCoordinates();
    this.paintCanvas(this.coords, this.currentColor);
  }

  applyCircleToCanvas(event) {
    this.prepareForDrawingShapes(event);
    this.getCoordinatesForCircle(event);
    this.minimizeCoordinates();
    this.paintCanvas(this.coords, this.currentColor);
  }

  applyEllipseToCanvas(event) {
    this.prepareForDrawingShapes(event);
    this.getCoordinatesForEllipse(event);
    this.minimizeCoordinates();
    this.paintCanvas(this.coords, this.currentColor);
  }

  applyLightenToCanvas(event, lightness, frame) {
    this.getCoordinatesForStroke(event);
    this.minimizeCoordinates();
    this.changeCanvasLightning(lightness, frame);
  }

  applyDitheringToCanvas(event) {
    this.getCoordinatesForStroke(event);
    this.minimizeCoordinates();
    if (event.which === 1) {
      this.ditherCanvas(this.coords, this.primaryColor, this.secondaryColor);
    }
    if (event.which === 3) {
      this.ditherCanvas(this.coords, this.secondaryColor, this.primaryColor);
    }
  }

  eraseCanvas(event) {
    this.getCoordinatesForStroke(event);
    this.minimizeCoordinates();
    this.element.erase(this.coords);
    this.image = this.createImage();
  }

  applyLayerOpacity() {
    this.layers.forEach(layer => $(`#${layer.elementId}`).css('--index', 1));
    $(`#${this.element.elementId}`).css('--index', 0);
  }

  changeLayerOpacity(index) {
    this.layers.forEach(layer => $(`#${layer.elementId}`).css('--index', 1));
    $(`#${this.layers[index].elementId}`).css('--index', 0);
  }

  addNewLayer() {
    this.element = new ToolLayer(
      this.numberOfSquares, this.canvasWidth, this.baseColor, this.layers.length + 1,
    );
    this.layers.push(this.element);
    this.element.render(this.element.canvasContainerId);
    const offset = $(`#${this.layers[0].elementId}`).offset();
    const {
      top,
      left,
    } = offset;
    $(`#${this.element.elementId}`).offset({ top, left });
    this.applyLayerOpacity();
    this.listen();
  }

  removeLayer(event) {
    if (this.layers.length > 1) {
      $(`#${this.element.elementId}`).remove();
      const index = this.layers.indexOf(this.element);
      if (index === 0) {
        this.element = this.layers[index + 1];
      } else {
        this.element = this.layers[index - 1];
      }
      this.layers.splice(index, 1);
      this.refreshLayersOffset();
      const { layer } = event.data;
      this.drawCurrentFrame(layer);
    }
  }

  drawUpdatedFrames(updatedFrames) {
    if (!this.draftLayer) {
      this.draftLayer = new ToolLayer(
        this.numberOfSquares, this.canvasWidth, this.baseColor, -1,
      );
      this.draftLayer.render(this.element.canvasContainerId);
      this.refreshLayersOffset();
    }
    $(`#${this.draftLayer.elementId}`).css('z-index', '-1');
    const updatedImages = new Array(updatedFrames.length);
    for (let i = 0; i < updatedFrames.length; i += 1) {
      updatedImages[i] = new Array(0);
      for (let j = 0; j < updatedFrames[i].length; j += 1) {
        this.draftLayer.drawNewFrame(updatedFrames[i][j]);
        updatedImages[i].push(this.draftLayer.createImage());
      }
    }
    this.draftLayer.clear();
    const event = new Event(EVENT_ENUM.IMAGES_UPDATE, updatedImages);
    this.notify(event);
  }

  resizeCanvas(eventData) {
    const { value, layer } = eventData;
    const prevValue = this.numberOfSquares;
    this.numberOfSquares = value;
    for (let i = 0; i < this.layers.length; i += 1) {
      this.layers[i].size = value;
      this.layers[i].squareWidth = this.layers[i].width / value;
    }
    if (this.draftLayer) {
      this.draftLayer.size = value;
      this.draftLayer.squareWidth = this.draftLayer.width / value;
    }
    if (this.canvasAvatar) {
      this.canvasAvatar.size = value;
      this.canvasAvatar.squareWidth = this.canvasAvatar.width / value;
    }
    this.drawCurrentFrame(layer);
    if (prevValue > value) {
      const { updatedFrames } = eventData;
      this.drawUpdatedFrames(updatedFrames);
    }
  }

  drawInitialCanvas(eventData) {
    const {
      currentLayerIndex,
      currentFrame,
    } = eventData;
    this.numberOfSquares = currentFrame[0].length;
    if (currentFrame.length < this.layers.length) {
      let index = this.layers.length - 1;
      while (index >= currentFrame.length) {
        $(`#${this.layers[index].elementId}`).remove();
        this.layers.pop();
        index -= 1;
      }
    } else if (currentFrame.length > this.layers.length) {
      for (let i = this.layers.length; i < currentFrame.length; i += 1) {
        this.addNewLayer();
      }
    }
    this.changeCurrentLayer({ index: currentLayerIndex });
    for (let i = 0; i < this.layers.length; i += 1) {
      this.layers[i].size = this.numberOfSquares;
      this.layers[i].squareWidth = this.layers[i].width / this.layers[i].size;
    }
    if (this.draftLayer) {
      this.draftLayer.size = this.numberOfSquares;
    }
    if (this.canvasAvatar) {
      $(`#${this.canvasAvatar.elementId}`).remove();
    }
    this.drawCurrentFrame(currentFrame);
  }

  moveLayer(eventData, index) {
    const { layer } = eventData;
    this.drawCurrentFrame(layer);
    this.changeLayerOpacity(index);
    this.element = this.layers[index];
  }

  changeCurrentLayer(eventData) {
    const { index } = eventData;
    this.changeLayerOpacity(index);
    this.element = this.layers[index];
  }

  handleMouseMove(event) {
    if (this.locked) {
      event.preventDefault();
      this.minimizeCoordinates();
      const e = new Event(EVENT_ENUM.CANVAS_MOUSEMOVE, event, this.coords);
      this.notify(e);
      this.drawn = true;
    }
    this.handleCanvasMouseover(event);
    this.minimizeCoordinates();
    [...this.finalCoords] = [...this.coords];
    this.coords = [];
    this.coordsMinimized = false;
  }

  handleCanvasMouseover(event) {
    const coords = this.getCoordinates(event);
    const settingsEvent = new AppSettingsEvent(
      SETTINGS_EVENT_ENUM.COORDINATES_CHANGE, coords[0],
    );
    this.notify(settingsEvent);
  }

  handleMouseLeave(event) {
    if (this.locked) {
      const e = new Event(EVENT_ENUM.CANVAS_MOUSELEAVE, event, this.coords);
      this.notify(e);
      this.locked = false;
    }
    const settingsEvent = new AppSettingsEvent(SETTINGS_EVENT_ENUM.CANVAS_MOUSE_LEAVE);
    this.notify(settingsEvent);
  }

  notifyChange(event) {
    if (event.type === MODEL_EVENT_ENUM.CURRENT_FRAME_CHANGE
      || event.type === MODEL_EVENT_ENUM.CURRENT_FRAME_DUPLICATE
      || event.type === MODEL_EVENT_ENUM.CURRENT_FRAME_DELETE
      || event.type === MODEL_EVENT_ENUM.CURRENT_FRAME_UPDATE) {
      this.drawCurrentFrame(event.data);
    }
    if (event.type === MODEL_EVENT_ENUM.CURRENT_FRAME_UPDATE) {
      const e = new Event(EVENT_ENUM.IMAGE_UPDATE);
      this.notify(e);
    }
    if (event.type === MODEL_EVENT_ENUM.FRAME_ADD) {
      this.clearCanvas();
      this.image = this.createImage();
      const e = new Event(EVENT_ENUM.ADD_EMPTY_FRAME);
      this.notify(e);
    }
    if (event.type === MODEL_EVENT_ENUM.SHAPE_SELECT) {
      this.lockCanvasWithMove();
      this.canvasAvatar.drawShape(event.data);
      this.canvasAvatar.shapeLocked = true;
    }
    if (event.type === LAYER_EVENT_ENUM.ADD_LAYER) {
      this.addNewLayer();
    }
    if (event.type === LAYER_EVENT_ENUM.REMOVE_LAYER) {
      this.removeLayer(event);
    }
    if (event.type === LAYER_EVENT_ENUM.MOVE_LAYER_UP) {
      this.moveLayer(event.data, event.data.index + 1);
    }
    if (event.type === LAYER_EVENT_ENUM.MOVE_LAYER_DOWN) {
      this.moveLayer(event.data, event.data.index - 1);
    }
    if (event.type === MODEL_EVENT_ENUM.CANVAS_RESIZE) {
      this.resizeCanvas(event.data);
    }
    if (event.type === MODEL_EVENT_ENUM.CURRENT_LAYER_CHANGE) {
      this.changeCurrentLayer(event.data);
    }
    if (event.type === MODEL_EVENT_ENUM.SET_INITIAL_STATE) {
      this.drawInitialCanvas(event.data);
    }
  }

  listenToCanvasAvatar() {
    $(`#${this.canvasAvatar.elementId}`).on({
      mousedown: (event) => {
        $(`#${this.canvasAvatar.elementId}`).css('cursor', 'grabbing');
        this.lockAvatar(event);
        return false;
      },
      mousemove: (event) => {
        event.preventDefault();
        if (this.canvasAvatar.locked) {
          this.handleAvatarMove(event);
        }
        return false;
      },
      mouseup: (event) => {
        if (this.canvasAvatar.locked) {
          this.unlockAvatar(event);
        }
        return false;
      },
      mouseover: (event) => {
        this.handleCanvasMouseover(event);
      },
      mouseleave: () => {
        const settingsEvent = new AppSettingsEvent(SETTINGS_EVENT_ENUM.CANVAS_MOUSE_LEAVE);
        this.notify(settingsEvent);
      },
    });
    $('body').on({
      keydown: (event) => {
        if (event.key === 'Enter') {
          event.preventDefault();
          this.unlockAvatar(event);
        }
      },
    });
  }

  listen() {
    $(`#${this.element.elementId}`).on({
      mousedown: (event) => {
        this.coords = [];
        const e = new Event(EVENT_ENUM.CANVAS_MOUSEDOWN, event);
        this.notify(e);
      },
      mousemove: (event) => {
        this.handleMouseMove(event);
      },
      mouseup: (event) => {
        if (this.locked) {
          event.preventDefault();
          const e = new Event(EVENT_ENUM.CANVAS_MOUSEUP, event, this.finalCoords);
          this.notify(e);
          this.locked = false;
          this.strokeLocked = false;
        } else {
          this.getInitialCoordinates(event);
          const e = new Event(EVENT_ENUM.CANVAS_CLICK, event, this.coords);
          this.notify(e);
        }
      },
      mouseleave: (event) => {
        this.handleMouseLeave(event);
      },
      mouseover: (event) => {
        this.handleCanvasMouseover(event);
      },
      contextmenu: (event) => {
        event.preventDefault();
      },
    });
  }
}
