export default class AppFrameList {
  constructor() {
    this.frames = [];
  }

  addFrame(frame) {
    this.frames.push(frame);
  }

  getImages() {
    const images = [];
    this.frames.forEach(frame => images.push(frame.images));
    return images;
  }

  getCurrentLayers() {
    const currentLayers = [];
    this.frames.forEach(frame => currentLayers.push(frame.currentLayer));
    return currentLayers;
  }

  refreshIndices(index) {
    for (let i = index; i < this.frames.length; i += 1) {
      this.frames[i].id -= 1;
    }
  }

  getFrames() {
    const updatedFrames = [];
    this.frames.forEach(frame => updatedFrames.push(frame.layers));
    return updatedFrames;
  }

  setFrames(frames) {
    [...this.frames] = [...frames];
  }
}
