import $ from 'jquery';
import './toolButtonStyle.less';

export default class ToolButton {
  constructor(container, className, id) {
    this.container = container;
    this.template = `<button class="button" id="${id}">
      <i class="tool-button__icon ${className}" data-attribute="${id}"></i>
    </button>`;
  }

  render() {
    $(`#${this.container}`).append(this.template);
  }
}
